import { defineStore } from 'pinia';

export const usePatientStore = defineStore({
  id: 'patient-store',
  state: () => ({
    patients: [
      {
        id: 0,
        name: 'Jenna',
        lastname: 'Charles',
        dob: '01/02/1988',
        breathingrate: '60',
        age: '32',
        weight: 65,
        height: 175,
        gender: 'Female',
        scandate: '01/02/2021',
        scanlocation: 'Area 19 Office',
      },
      {
        id: 1,
        name: 'Benny',
        lastname: 'Adams',
        dob: '01/02/1989',
        breathingrate: '60',
        age: '32',
        weight: 65,
        height: 175,
        gender: 'Male',
        scandate: '01/02/2022',
        scanlocation: 'Montreal',
      },
      {
        id: 2,
        name: 'Emma',
        lastname: 'Blanco',
        dob: '01/02/1992',
        breathingrate: '60',
        age: '32',
        weight: 65,
        height: 175,
        gender: 'Female',
        scandate: '01/02/2023',
        scanlocation: 'MUHC HQ',
      },
    ],
    searchTerm: '',
  }),
  getters: {
    //
  },
  actions: {
    //
    addPatient(patient) {
      this.patients.push(patient);
    },

    deletePatient(patientId) {
      const indexToRemove = this.patients.findIndex(
        (patient) => patient.id === patientId
      );

      if (indexToRemove !== -1) {
        this.patients.splice(indexToRemove, 1);
      }
    },
    getPatientbyID(patientId) {
      const targetPatient = this.patients.findIndex(
        (patient) => patient.id === patientId
      );

      if (targetPatient !== -1) {
        return this.patients[targetPatient];
      }
      return null;
    },
  },
});
