<script lang="ts">
import { onKeyDown } from '@vueuse/core';
import { Maybe } from '@/src/types';
import { useCurrentImage } from '@/src/composables/useCurrentImage';
import { useToolStore } from '@/src/store/tools';
import useContourStore, { ContourToolMode } from '@/src/store/tools/contours';
import { Tools } from '@/src/store/tools/types';
import { useViewStore } from '@/src/store/views';
import { LPSAxisDir } from '@/src/types/lps';
import { frameOfReferenceToImageSliceAndAxis } from '@/src/utils/frameOfReference';
import { getLPSAxisFromDir } from '@/src/utils/lps';
import type { Vector2 } from '@kitware/vtk.js/types';
import vtkWidgetManager from '@kitware/vtk.js/Widgets/Core/WidgetManager';
import {
  computed,
  defineComponent,
  ref,
  toRefs,
  watch,
  PropType,
  onUnmounted,
  reactive,
} from 'vue';
import { storeToRefs } from 'pinia';
import {
  ReferencePlane,
  referencePlaneToFrame,
  getReferencePlaneFromSlice,
} from '@/src/core/contours/referencePlane';
import type { ContourID } from '@/src/types/contour';
import ContourWidget2D from './contour/ContourWidget2D.vue';

const AutoAddDrawingContourModes = new Set([
  ContourToolMode.FreehandDraw,
  ContourToolMode.PointPlacing,
  ContourToolMode.Threshold,
]);

function fequals(a: number, b: number, eps = 1e-6) {
  return Math.abs(a - b) < eps;
}

export default defineComponent({
  components: { ContourWidget2D },
  props: {
    viewId: {
      type: String,
      required: true,
    },
    currentSlice: {
      type: Number,
      required: true,
    },
    viewDirection: {
      type: String as PropType<LPSAxisDir>,
      required: true,
    },
    widgetManager: {
      type: Object as PropType<vtkWidgetManager>,
      required: true,
    },
  },
  setup(props) {
    const { viewId, viewDirection, currentSlice } = toRefs(props);
    const toolStore = useToolStore();
    const contourStore = useContourStore();
    const { currentImageID, currentImageMetadata } = useCurrentImage();
    const viewAxis = computed(() => getLPSAxisFromDir(viewDirection.value));
    const { mode: toolMode, selectedContourID } = storeToRefs(contourStore);

    const drawingContourID = ref<Maybe<ContourID>>(null);

    const isToolActive = computed(
      () => toolStore.currentTool === Tools.Contour
    );
    const viewType = computed(() => {
      const viewStore = useViewStore();
      return viewStore.viewSpecs[viewId.value].viewType;
    });

    type IsContourInCurrentViewOptions = {
      checkImageID?: boolean;
      checkSlice?: boolean;
    };

    const isContourInCurrentView = (
      contourId: ContourID,
      opts?: IsContourInCurrentViewOptions
    ) => {
      const { toolByID } = contourStore;
      if (!(contourId in toolByID)) return false;

      const { referencePlane, imageID } = toolByID[contourId];

      const frameOfReference = referencePlaneToFrame(referencePlane);
      const axisAndSlice = frameOfReferenceToImageSliceAndAxis(
        frameOfReference,
        currentImageMetadata.value,
        {
          allowOutOfBoundsSlice: true,
        }
      );
      if (!axisAndSlice) return false;

      const checkImageID = opts?.checkImageID ?? false;
      const checkSlice = opts?.checkSlice ?? false;
      const { axis, slice } = axisAndSlice;
      return (
        axis === viewAxis.value &&
        (!checkImageID || imageID === currentImageID.value) &&
        (!checkSlice || fequals(slice, currentSlice.value))
      );
    };

    const contours = computed(() => {
      return contourStore.toolIDs
        .filter((id) => {
          // only show contours for the current image and view axis
          return isContourInCurrentView(id, {
            checkImageID: true,
            checkSlice: true,
          });
        })
        .map((id) => contourStore.toolByID[id])
        .filter(Boolean);
    });

    // --- active/drawing contour management -- //

    const commitDrawingContour = () => {
      drawingContourID.value = null;
    };

    const addDrawingContour = () => {
      if (currentImageID.value && !drawingContourID.value) {
        drawingContourID.value = contourStore.addTool({
          imageID: currentImageID.value,
        });
      }
    };

    const clearDrawingContour = () => {
      if (drawingContourID.value != null) {
        contourStore.removeTool(drawingContourID.value);
        drawingContourID.value = null;
      }
    };

    // update the drawing contour's label to the active label
    const { activeLabel } = storeToRefs(contourStore);

    watch(
      [activeLabel, drawingContourID],
      ([label, contourID]) => {
        if (contourID) {
          contourStore.updateTool(contourID, {
            label,
          });
        }
      },
      { immediate: true }
    );

    watch(
      [isToolActive, currentImageID],
      ([active, imageID]) => {
        clearDrawingContour();
        if (active && imageID) {
          addDrawingContour();
        }
      },
      { immediate: true }
    );

    watch(
      toolMode,
      (mode) => {
        clearDrawingContour();
        if (AutoAddDrawingContourModes.has(mode)) {
          addDrawingContour();
        }
      },
      { immediate: true }
    );

    onUnmounted(() => {
      clearDrawingContour();
    });

    const onContourFinalized = (id: ContourID) => {
      if (id === drawingContourID.value) {
        commitDrawingContour();
        addDrawingContour();
      }
    };

    // --- updating active/drawing contour frame --- //

    function getCurrentReferencePlane(): ReferencePlane {
      const { lpsOrientation, indexToWorld } = currentImageMetadata.value;
      const sliceAxis = lpsOrientation[viewAxis.value];
      return getReferencePlaneFromSlice(
        indexToWorld,
        currentSlice.value,
        sliceAxis
      );
    }

    // update active/drawing contour's frame + slice, since the
    // active/drawing contour is not finalized.
    watch(
      [currentSlice, drawingContourID] as const,
      ([slice, contourID]) => {
        if (!contourID) return;
        contourStore.updateTool(contourID, {
          slice,
          referencePlane: getCurrentReferencePlane(),
        });
      },
      { immediate: true }
    );

    // --- selected contour --- //

    const onContourSelected = (id: ContourID, selected: boolean) => {
      if (selected) {
        contourStore.selectContour(id);
      } else if (id === selectedContourID.value) {
        contourStore.selectContour(null);
      }
    };

    watch([currentSlice, currentImageID], () => {
      const selected = selectedContourID.value;
      if (!selected) return;

      if (
        isContourInCurrentView(selected, {
          checkImageID: true,
          checkSlice: true,
        })
      ) {
        contourStore.selectContour(null);
      }
    });

    onKeyDown(['Delete', 'Backspace'], (ev) => {
      ev.preventDefault();
      if (selectedContourID.value) {
        contourStore.removeTool(selectedContourID.value);
      }
    });

    // --- context menu --- //

    const contextMenu = reactive({
      visible: false,
      x: 0,
      y: 0,
      id: '' as ContourID,
    });

    const onContextMenu = (id: ContourID, displayXY: Vector2) => {
      [contextMenu.x, contextMenu.y] = displayXY;
      contextMenu.visible = true;
      contextMenu.id = id;
    };

    const deleteFromContextMenu = () => {
      contourStore.removeTool(contextMenu.id);
    };

    const contextMenuLabels = computed(() => {
      const { id: contourID } = contextMenu;
      if (!(contourID in contourStore.toolByID)) {
        return [];
      }

      const contour = contourStore.toolByID[contourID];
      return Object.entries(contourStore.labels).map(([labelID, label]) => ({
        id: labelID,
        name: label.labelName,
        isCurrent: contour.label === labelID,
      }));
    });

    const setLabelFromContextMenu = (label: string) => {
      contourStore.updateTool(contextMenu.id, { label });
    };

    // --- //

    return {
      isToolActive,
      is2D: computed(() => viewType.value === '2D'),
      contours,
      drawingContourID,
      selectedContourID,
      toolMode: computed(() =>
        isToolActive.value ? toolMode.value : ContourToolMode.Select
      ),
      contextMenu,
      contextMenuLabels,
      onContourFinalized,
      onContourSelected,
      onContextMenu,
      deleteFromContextMenu,
      setLabelFromContextMenu,
    };
  },
});
</script>

<template>
  <div v-if="is2D" class="overlay-no-events">
    <ContourWidget2D
      v-for="contour in contours"
      :key="contour.id"
      :view-id="viewId"
      :current-slice="currentSlice"
      :contour-id="contour.id"
      :view-direction="viewDirection"
      :widget-manager="widgetManager"
      :drawing="drawingContourID === contour.id"
      :mode="toolMode"
      :selected="selectedContourID === contour.id"
      @finalize="onContourFinalized(contour.id)"
      @select="onContourSelected(contour.id, $event)"
      @contextmenu="onContextMenu(contour.id, $event)"
    />
    <v-menu
      v-model="contextMenu.visible"
      :style="{
        position: 'absolute',
        top: `${contextMenu.y}px`,
        left: `${contextMenu.x}px`,
      }"
      close-on-click
      close-on-content-click
    >
      <v-list dense>
        <v-list-item @click.stop>
          <v-list-item-title
            class="d-flex flex-row align-center justify-space-betwen"
          >
            <span>Change label to...</span>
            <v-icon>mdi-chevron-right</v-icon>
          </v-list-item-title>
          <v-menu open-on-hover activator="parent" location="end">
            <v-list dense>
              <v-list-item v-for="(label, idx) in contextMenuLabels" :key="idx">
                <v-radio
                  :label="label.name"
                  :model-value="label.isCurrent"
                  @click="setLabelFromContextMenu(label.id)"
                >
                </v-radio>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-list-item>
        <v-list-item @click="deleteFromContextMenu">
          <v-list-item-title>Delete</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </div>
</template>

<style scoped src="@/src/components/styles/vtk-view.css"></style>
