<template>
  <div
    @click="handleclick"
    class="circle-container"
    :class="{ clicked: activeStudyStore.getProperty(sliceID, propertyName) }"
    @keydown.space.prevent="handleKeyDown"
  >
    <div class="icon-container">
      <img
        :src="
          '/src/components/icons/app_icons/' + iconSource + activeIcon + '.svg'
        "
        :style="{ height: iconHeight + 'px' }"
        alt="Icon"
        @click="handleClick"
        @keydown.space.prevent="handleKeyDown"
      />
    </div>
  </div>
</template>

<script>
import { useActiveStudyStore } from '@/src/store/active-study';

export default {
  props: {
    sliceID: { type: Number },
    propertyName: { type: String },
    iconSource: { type: String, default: 'input-cursor' },
    iconHeight: { type: Number, default: 32 },
  },
  data() {
    return {
      activeStudyStore: useActiveStudyStore(),
      activeIcon: '',
    };
  },
  mounted() {
    if (this.activeStudyStore.getProperty(this.sliceID, this.propertyName)) {
      this.activeIcon = '_Active';
    }
  },
  methods: {
    handleclick() {
      this.activeStudyStore.toggleProperty(this.sliceID, this.propertyName);

      this.activeIcon = this.activeStudyStore.getProperty(
        this.sliceID,
        this.propertyName
      )
        ? '_Active'
        : '';
    },
  },
};
</script>

<style scoped>
.circle-container {
  border-radius: 50px;
  background-color: #000000ab;
  height: 32px;
  width: 32px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease-in-out;
}

.circle-container:hover {
  background-color: #ffffff1e;
}

.circle-container.clicked {
  background-color: #ffffff1e;
}

.icon-container {
  display: flex;
  align-content: center;
  justify-content: center;
  cursor: pointer;
  opacity: 1;
  transition: all 0.3s ease-in-out;
}

.icon-container.clicked {
  opacity: 1;
  filter: invert(57%) sepia(95%) saturate(558%) hue-rotate(359deg)
    brightness(101%) contrast(107%);
}
</style>
