import { toRaw } from 'vue';
import macro from '@kitware/vtk.js/macros';
import vtkWidgetState from '@kitware/vtk.js/Widgets/Core/WidgetState';
import bounds from '@kitware/vtk.js/Widgets/Core/StateBuilder/boundsMixin';
import scale3 from '@kitware/vtk.js/Widgets/Core/StateBuilder/scale3Mixin';
import visible from '@kitware/vtk.js/Widgets/Core/StateBuilder/visibleMixin';
import origin from '@kitware/vtk.js/Widgets/Core/StateBuilder/originMixin';
import orientation from '@kitware/vtk.js/Widgets/Core/StateBuilder/orientationMixin';
import { vec2 } from 'gl-matrix';
import { transform3dTo2d } from '@/src/core/contours/referencePlane';
import { hexaToRGBA } from '@/src/utils/color';

function createCircleContextState(publicAPI, model, initialValues) {
  vtkWidgetState.extend(publicAPI, model, initialValues);
  scale3.extend(publicAPI, model, initialValues);
  visible.extend(publicAPI, model, initialValues);
  orientation.extend(publicAPI, model, initialValues);
  origin.extend(publicAPI, model, initialValues);
}

const newCircleContextStateInstance = macro.newInstance(
  createCircleContextState,
  'vtkCircleContextContourWidgetState'
);

function vtkContourWidgetState(publicAPI, model) {
  model.classHierarchy.push('vtkContourWidgetState');

  model._circleContextState = newCircleContextStateInstance();
  publicAPI.bindState(model._circleContextState, ['circleContext']);

  publicAPI.getCircleContext = () => model._circleContextState;

  function getContour() {
    return model._store.toolByID[model.id];
  }

  // Avoid proxy access/tracking on polyline reference
  publicAPI.getPolyLine = () => toRaw(getContour()?.polyLine);
  publicAPI.getReferencePlane = () => getContour()?.referencePlane;

  publicAPI.getPolyLineLength = () => {
    const pl = getContour()?.polyLine;
    return pl ? pl.length / 2 : 0;
  };

  const point2d = vec2.create();

  /**
   * @param {Vector3} coord
   */
  publicAPI.add3dPointToPolyLine = (coord) => {
    const pl = publicAPI.getPolyLine();
    const refPlane = publicAPI.getReferencePlane();
    if (!pl || !refPlane) return;

    transform3dTo2d(coord, refPlane, point2d);
    publicAPI.add2dPointToPolyLine(point2d);
  };

  /**
   * @param {Vector2} coord
   */
  publicAPI.add2dPointToPolyLine = (coord) => {
    const pl = publicAPI.getPolyLine();
    if (!pl) return;

    pl.push(...coord);
    publicAPI.modified();
  };

  publicAPI.clearPolyLine = () => {
    const pl = publicAPI.getPolyLine();
    if (!pl) return;

    pl.length = 0;
    publicAPI.modified();
  };

  /**
   * Takes a polyline comprised of 2D points in the contour's frame of reference.
   * @param polyLine
   */
  publicAPI.setPolyLine = (polyLine) => {
    const contour = getContour();
    if (!contour) return;

    contour.polyLine = polyLine;
    publicAPI.modified();
  };

  publicAPI.getColor = () => {
    const label = getContour()?.label;
    if (label) {
      // color components needs to be in [0,1]
      return hexaToRGBA(model._store.labels[label].color).map((c) => c / 255);
    }
    return null;
  };
}

const defaultValues = (initialValues) => ({
  finalized: false,
  selected: false,
  ...initialValues,
});

function createContourState(publicAPI, model, initialValues) {
  Object.assign(model, defaultValues(initialValues));
  vtkWidgetState.extend(publicAPI, model, initialValues);
  bounds.extend(publicAPI, model);

  macro.get(publicAPI, model, ['id']);
  macro.setGet(publicAPI, model, ['finalized', 'selected']);
  macro.moveToProtected(publicAPI, model, ['store']);
  macro.get(publicAPI, model, ['_store']);

  if (!model.id) {
    throw new Error('No ID provided!');
  }

  if (!model._store) {
    throw new Error('No store provided!');
  }

  vtkContourWidgetState(publicAPI, model);
}

// ----------------------------------------------------------------------------

export const newInstance = macro.newInstance(
  createContourState,
  'vtkContourWidgetState'
);

// ----------------------------------------------------------------------------

export default function generateState(initialValues) {
  return newInstance(initialValues);
}
