<!-- 
    BUTTON TO ADD A NEW PATIENT. 
    WHEN BUTTON IS CLICKED AN OVERLAY SHOWS WITH OPTIONS FOR ADDING PATIENT DATA.
    OVERLAY ALLOWS USER TO CLICK AND UPLOAD FILE OR DRAG AND DROP FILE TO UPLOAD.
    COMPONENT PART OF © AREA 19, OCG WEB APP.
    DEVELOPPED IN MONTREAL, QC, CANADA.
    JULY 2023.
  -->

<template>
  <div style="position: relative">
    <button @click="openOverlay">
      <!-- The button triggers the 'openOverlay' method when clicked to toggle showOverlay. -->
      Add New Patient
      <i class="fa-solid fa-user-plus" style="color: #ffffff"></i>
      <!-- The button text and an icon for adding a new patient. -->
    </button>
    <drag-and-drop enabled @drop-files="openFiles" id="app-container">
      <div
        class="overlay"
        :class="{ show: showOverlay }"
        @dragover="handleDragOver"
      >
        <!-- 
      The overlay is shown only when 'showOverlay' property is true. 
    -->

        <!-- Only show text and icons after overlay has been fully shown. -->
        <div class="add-patient-header" :class="{ visible: showTextOverlay }">
          <i
            @click="openOverlay"
            @keydown="handleKeyDown"
            class="fa-solid fa-xmark fa-xl"
            style="color: #ffffff"
          ></i>
          <!-- 
        The close icon allows users to close the overlay by calling 'openOverlay'
        method when clicked. 
      -->
        </div>

        <div
          @click="openFileDialog"
          @dragenter="handleDragEnter"
          @dragleave="handleDragLeave"
          @keydown="handleKeyDown"
          class="load-files"
          :class="{ visible: showTextOverlay }"
        >
          <!-- 
        The 'load-files' section allows users to load files to add a new study.
        It triggers the 'openFileDialog' method when clicked.
      -->

          <input
            type="file"
            ref="fileInput"
            style="display: none"
            @change="handleFileChange"
            aria-labelledby="fileInput"
          />

          <!-- 
        The hidden file input element allows users to select files from their device,
        which will trigger the 'handleFileChange' method when files are selected.
      -->
          <i class="fa-solid fa-file-import fa-2xl" style="color: #ffffff"></i>
          <!-- An icon representing the file import functionality. -->
          <br />
          <h2>{{ loadFileText }}</h2>
          <!-- Heading for the 'Load Files' section. -->
          <h4>{{ browseTextFile }}</h4>
          <!-- Description for the 'Load Files' section. -->
        </div>
      </div>
    </drag-and-drop>
  </div>
  <div style="display: none">
    <CheckContours />
  </div>
</template>

<script>
import { useDatasetStore } from '@/src/store/datasets';
import { useDICOMStore } from '@/src/store/datasets-dicom';
import { usePatientStore } from '@/src/store/patient-data';
// import { useImageStore } from '@/src/store/datasets-images';
import {
  importDataSources,
  convertSuccessResultToDataSelection,
} from '@/src/io/import/importDataSources';
import CheckContours from './CheckContours.vue';
import DragAndDrop from './DragAndDrop.vue';

import { fileToDataSource } from '../io/import/dataSource';
import { partition } from '../utils';

export default {
  components: {
    DragAndDrop,
    CheckContours,
  },

  data() {
    /**
     * Data properties of the component.
     * @returns {Object} An object containing the component's data properties.
     */
    return {
      showOverlay: false, // A flag to control the visibility of the overlay.
      showTextOverlay: false, // A flag to control the visibility of the text inside the overlay.
      isDraggingOver: false,
      loadFileText: 'Load Files',
      browseTextFile: 'Browse your data to automatically add a new study.',
      dataStore: useDatasetStore(),
      patientStore: usePatientStore(),
      currentPatientStoreID: null,
    };
  },
  methods: {
    /**
     * Toggles the overlay and sets a timeout to show the text overlay after a delay if the overlay is shown.
     */
    async openFiles(file) {
      console.log(file);
      const dataSources = Array.from(file).map(fileToDataSource);

      console.log(dataSources);
      this.loadFiles(dataSources);
    },

    async loadFiles(sources) {
      const results = await importDataSources(sources);

      console.log(results);

      this.currentPatientStoreID = results[0].data[0].dataID;
      const partitionResult = partition((result) => result.ok, results);
      const succeeded = partitionResult[0];

      if (!this.dataStore.primarySelection && succeeded.length) {
        const selection = convertSuccessResultToDataSelection(succeeded[0]);
        if (selection) {
          this.dataStore.setPrimarySelection(selection);
        }
      }

      // const imageStore = useImageStore();
      const dicomStore = useDICOMStore();

      console.log(dicomStore);
      const volumeKey = this.currentPatientStoreID;
      // window.sessionStorage.setItem('target-volume-key', volumeKey);
      const volumeInfo = dicomStore.volumeInfo[volumeKey];
      const studyKey = dicomStore.volumeStudy[volumeKey];
      const studyInfo = dicomStore.studyInfo[studyKey];
      const patientKey = dicomStore.studyPatient[studyKey];
      const patientInfo = dicomStore.patientInfo[patientKey];

      // const image = await dicomStore.buildVolume(volumeKey);
      // const pixelArray = image.getPointData().getScalars().getData();
      // const dimensions = image.getDimensions();

      // console.log('Images:');
      // console.log(pixelArray);
      // console.log(dimensions);
      // console.log('--------------------');
      // console.log('key Here');
      // console.log(volumeKey);
      // console.log(volumeInfo);
      // console.log(studyInfo);
      // console.log(patientInfo);
      // console.log(patientInfo.PatientName);

      const patient = {
        id: volumeKey,
        name: patientInfo.PatientID,
        lastname: patientInfo.PatientName,
        dob: patientInfo.PatientBirthDate,
        gender: patientInfo.PatientSex,
        scandate: studyInfo.StudyDate,
        scanlocation: volumeInfo.SeriesDescription,
      };

      this.patientStore.addPatient(patient);

      // this.$emit('new-patient', patient);

      this.openOverlay();
    },

    handleKeyDown(event) {
      console.log(event.key);
      if (event.key === ' ') {
        this.showOverlay();
      }
    },
    openOverlay() {
      if (this.showOverlay) {
        this.showTextOverlay = false;
      }
      this.showOverlay = !this.showOverlay;
      if (this.showOverlay) {
        /**
         * Set a timeout to show the text overlay after a delay when the overlay is shown.
         * This creates a smooth transition effect.
         */
        setTimeout(() => {
          this.showTextOverlay = true;
        }, 200);
      }
    },

    /**
     * Opens the file dialog by programmatically clicking the hidden file input element.
     * Triggered when the user wants to select a file from their device.
     */
    openFileDialog() {
      this.$refs.fileInput.click();
    },

    /**
     * Handles the change event when a file is selected through the file input element.
     * @param {Event} event - The event object representing the file change event.
     */
    handleFileChange(event) {
      const selectedFile = event.target.files[0];
      this.openFiles(selectedFile);
      // Handle the selected file as needed
      // console.log('Selected File:', selectedFile);
    },
    /**
     * Handles the dragenter event when a file is dragged over the overlay.
     * @param {Event} event - The event object representing the dragenter event.
     */
    handleDragEnter(event) {
      event.preventDefault();
      this.isDraggingOver = true;
    },

    /**
     * Handles the dragover event when a file is being dragged over the overlay.
     * @param {Event} event - The event object representing the dragover event.
     */
    handleDragOver(event) {
      event.preventDefault();
      this.isDraggingOver = true;
      this.loadFileText = 'Drop File to Upload';
    },

    /**
     * Handles the dragleave event when a file is dragged out of the overlay.
     * @param {Event} event - The event object representing the dragleave event.
     */
    handleDragLeave(event) {
      event.preventDefault();
      this.isDraggingOver = false;
      this.loadFileText = 'Load Files';
    },

    /**
     * Handles the drop event when a file is dropped onto the overlay.
     * @param {Event} event - The event object representing the drop event.
     */
    // handleDrop(event) {
    //   event.preventDefault();
    //   this.isDraggingOver = false;
    //   this.loadFileText = 'Load Files';

    //   let files;
    //   if (event.dataTransfer) {
    //     // Attempt to access the files from the dataTransfer property directly.
    //     files = event.dataTransfer.files;
    //   } else if (event.target && event.target.files) {
    //     // If direct access doesn't work, try to access files from the event target.
    //     files = event.target.files;
    //   }

    //   console.log(files);
    // },
  },
};
</script>

<style scoped>
