  background: #00000080;
  padding: 10px;
  border-radius: 24px;
  width: 230px;
  min-width: 180px;
  height: 48px;
  align-self: center;
  position: relative;
  /* Making sure the button stays on top of the overlay:*/
  z-index: 99999;
  backdrop-filter: blur(30px);
  -webkit-backdrop-filter: blur(30px);
}

/* Button styles on hover */
button:hover {
  background: rgba(0, 0, 0, 0.65);
}

/* Margin for the icon */
i {
  margin-left: 10px;
}

/* Initial styles when the overlay is hidden */
.overlay {
  position: absolute;
  top: 0;
  width: 0;
  height: 0;
  padding: 0;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 15px;
  overflow: hidden;
  transition: width 0.3s, height 0.3s, padding 0.3s, background-color 0.3s;
}

/* Styles when the overlay is shown (expanded) */
.overlay.show {
  width: 50vw;
  height: 65vh;

  background-color: rgba(0, 0, 0, 0.849);
}

.add-patient-header {
  display: flex;
  height: 55px;
  justify-content: flex-end;
  align-items: center;
  padding: 20px;
}

.add-patient-header i {
  cursor: pointer;
}

.load-files {
  display: flex;
  flex-direction: column;
  gap: 5px;
  height: 80%;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

/* Animate the appearance */
.add-patient-header,
.load-files {
  opacity: 0; /* Initial opacity when hidden */
  transition: opacity 0.2s ease;
}

.add-patient-header.visible,
.load-files.visible {
  opacity: 1; /* Opacity when shown */
}

/* Responsive styles */
@media screen and (max-width: 767px) {
  /* Adjust the button size for smaller screens */
  button {
    min-width: 140px;
    height: 40px;
    font-size: 12px;
  }

  /* Adjust the overlay size for smaller screens */
  .overlay.show {
    padding: 5px;
    text-align: center;
  }

  /* Adjust alignment for smaller screens */
  .add-patient-header {
    justify-content: flex-start;
  }

  /* Adjust font size for smaller screens */
  .load-files {
    font-size: 12px;
  }
}
</style>
