import {
  ReferencePlane,
  createReferencePlane,
  transform2dTo3d,
} from '@/src/core/contours/referencePlane';
import vtkPolyData from '@kitware/vtk.js/Common/DataModel/PolyData';
import { vtkAlgorithm, vtkObject } from '@kitware/vtk.js/interfaces';
import macro from '@kitware/vtk.js/macro';
import { TypedArray, Vector2, Vector3 } from '@kitware/vtk.js/types';
import { vec2 } from 'gl-matrix';

interface vtkTransformPolyDataToPlane extends vtkObject, vtkAlgorithm {
  setRefPlane(plane: ReferencePlane): boolean;
  getRefPlane(): ReferencePlane;
  requestData(inData: [vtkPolyData], outData: [vtkPolyData]): void;
}

interface Model {
  [key: string]: any;
}

type TypedArrayConstructor =
  | Int8ArrayConstructor
  | Uint8ArrayConstructor
  | Uint8ClampedArrayConstructor
  | Int16ArrayConstructor
  | Uint16ArrayConstructor
  | Int32ArrayConstructor
  | Uint32ArrayConstructor
  | Float32ArrayConstructor
  | Float64ArrayConstructor;

function cloneArray(arr: number[] | TypedArray) {
  if (Array.isArray(arr)) {
    return [...arr];
  }
  return (arr.constructor as TypedArrayConstructor).from(arr);
}

/**
 * Input polydata is expected to have points on the (x,y,0) plane.
 */
function vtkTransformPolyDataToPlaneCtor(
  publicAPI: vtkTransformPolyDataToPlane,
  model: Model
) {
  model.classHierarchy.push('vtkTransformPolyDataToPlane');

  publicAPI.requestData = (inData, outData) => {
    const refPlane = publicAPI.getRefPlane() ?? createReferencePlane();

    const [inPd] = inData;

    if (!outData[0]) {
      outData[0] = vtkPolyData.newInstance();
    }
    const [output] = outData;

    const outPoints = Float32Array.from(inPd.getPoints().getData());
    output.getPoints().setData(outPoints);

    const pt2d: Vector2 = [0, 0];
    const pt3d: Vector3 = [0, 0, 0];
    for (let i = 0; i < outPoints.length; i += 3) {
      vec2.set(pt2d, outPoints[i], outPoints[i + 1]);
      transform2dTo3d(pt2d, refPlane, pt3d);
      /* eslint-disable prefer-destructuring */
      outPoints[i] = pt3d[0];
      outPoints[i + 1] = pt3d[1];
      outPoints[i + 2] = pt3d[2];
      /* eslint-enable prefer-destructuring */
    }

    const inLines = inPd.getLines().getData();
    output.getLines().setData(cloneArray(inLines));

    output.modified();
  };
}

// ----------------------------------------------------------------------------

const DEFAULT_VALUES = {
  refPlane: null,
};

// ----------------------------------------------------------------------------

export function extend(publicAPI: object, model: object, initialValues = {}) {
  Object.assign(model, DEFAULT_VALUES, initialValues);

  macro.obj(publicAPI, model);
  macro.algo(publicAPI, model, 1, 1);
  macro.setGet(publicAPI, model, ['refPlane']);

  vtkTransformPolyDataToPlaneCtor(
    publicAPI as vtkTransformPolyDataToPlane,
    model
  );
}

// ----------------------------------------------------------------------------

export const newInstance = macro.newInstance(
  extend,
  'vtkTransformPolyDataToPlane'
);

// ----------------------------------------------------------------------------

const vtkTransformPolyDataToPlane: {
  newInstance: typeof newInstance;
  extend: typeof extend;
} = { newInstance, extend };

export default vtkTransformPolyDataToPlane;
