<template>
  <!-- This is the main template for the component -->
  <div class="container" ref="background">
    <!-- The main container div with dynamic class binding for background image loading -->

    <div class="home">
      <PopUpDelete
        v-if="removePatientTarget"
        :message="`You are about to delete ${removePatientTarget.name} and all it's files. Are you sure you want to proceed?`"
        @closePopup="removePatientTarget = null"
        @confirmDelete="handleDeletePatient"
      />
      <!-- The home section of the component -->
      <HomeGreetings :username="username" />
      <!-- A component to greet the user with their username.
         Component takes username as a prop -->

      <br />
      <!-- Line break for spacing -->

      <div class="user-bar">
        <!-- Container for patient-related components -->

        <AddPatient @new-patient="handleNewPatient" />
        <!-- A component that handles adding new patients -->

        <FindPatient @search-term-updated="handleUpdateFilter" />
        <!-- A component to find a patient, emitting 'search-term-updated' event -->

        <LogOut style="margin-left: auto; align-self: center" />
        <!-- A component to log the user out, with custom styling -->
      </div>
      <!-- End of user-bar section -->

      <PatientContainer
        :patients="patients"
        :filter="patientStore.searchTerm"
        @removePatient="handleRemovePatient"
      />

      <!-- A container to display patient list along with patient information -->
      <h5 style="color: grey; margin-left: 90%">Area 19 OCG Prototype V.0.1</h5>
    </div>

    <!-- End of home section -->
  </div>
  <!-- End of main container div -->
</template>

<script>
import { usePatientStore } from '@/src/store/patient-data';
import { useAppState } from '@/src/store/ocg-app-state';

import HomeGreetings from '../components/HomeGreetings.vue';
import PatientContainer from '../components/PatientContainer.vue';

// import store from '/src/store/index.js';
import AddPatient from '../components/AddPatient.vue';
import FindPatient from '../components/FindPatient.vue';
import LogOut from '../components/LogOut.vue';
import PopUpDelete from '../components/PopUpDelete.vue';

export default {
  data() {
    return {
      username: null,
      filter: '',
      patients: usePatientStore().patients,
      patientStore: usePatientStore(),
      appState: useAppState(),
      removePatientTarget: '',
    };
  },
  components: {
    HomeGreetings,
    PatientContainer,
    AddPatient,
    FindPatient,
    LogOut,
    PopUpDelete,
  },

  mounted() {
    this.username = this.appState.currentUser;
    if (!this.username) {
      this.$router.push('/login');
    }

    this.$refs.background.style.backgroundImage = `url('/images/${this.timeofday.toLowerCase()}.jpg')`;
  },
  methods: {
    handleUpdateFilter(filter) {
      this.patientStore.searchTerm = filter;
    },
    handleRemovePatient(patient) {
      this.removePatientTarget = patient;
    },
    handleDeletePatient() {
      this.patientStore.deletePatient(this.removePatientTarget.id);
      // this.removePatientById(this.removePatientTarget.id);
      this.removePatientTarget = null;
    },
  },
  created() {
    this.timeofday = this.appState.timeOfDay;
    this.filter = this.patientStore.searchTerm;
  },
};
</script>

