<template>
  <!-- Adding a overlay for smaller screen sizes web app is not supported for. -->
  <div class="mobile-overlay">
    <div class="warning">
      <h2>
        It appears that you are trying to use our app on a screen size of less
        than 1350 px wide. <br />
        Currently our app is limited to larger devices. Please try on a larger
        laptop.
      </h2>
    </div>
  </div>
  <!-- Main container -->
  <div :class="container" ref="background">
    <!-- Body container -->
    <div class="body">
      <!-- Navigation Bar section -->
      <div class="navigationBar" ref="navigationBar">
        <!-- AnalysisNavigationBar component to handle view selection (patient list, quality check, analysis, report) -->
        <AnalysisNavigationBar />
      </div>
      <div class="header-main">
        <!-- Header section -->
        <div class="header" ref="header">
          <!-- PatientHeaderInfo component to display patient information and update quality check view -->
          <PatientHeaderInfo />
        </div>

        <!-- Main section -->
        <div
          class="main"
          ref="mainContainer"
          :style="{ width: mainWidth, height: mainHeight }"
        >
          <!-- Use fade transition when switching components -->
          <transition name="fade">
            <!-- Dynamic component based on currentComponent -->
            <component
              :is="appState.currentView"
              ref="mainAnalysisComponent"
              :width="appState.analysisScreenDims.width"
              :height="appState.analysisScreenDims.height"
            />
          </transition>
        </div>

        <!-- Version information -->
        <h5 style="margin-left: auto; color: grey">
          Area 19 OCG Prototype V.0.1
        </h5>
      </div>
    </div>
  </div>
</template>

<script>
import { useAppState } from '@/src/store/ocg-app-state';
import { useAnalysisStore } from '@/src/store/analysis';
import { reactive } from 'vue';

import PatientHeaderInfo from '../components/PatientHeaderInfo.vue';
import AnalysisNavigationBar from '../components/AnalysisNavigationBar.vue';
import QualityCheck from './QualityCheck.vue';
import ReportMaker from './ReportMaker.vue';
import GraphicalAnalysis from './GraphicalAnalysis.vue';
import CheckContourNew from '../components/CheckContourNew.vue';

export default {
  components: {
    PatientHeaderInfo,
    AnalysisNavigationBar,

    CheckContourNew,
    QualityCheck,
    ReportMaker,
    GraphicalAnalysis,
  },
  data() {
    return {
      // Data properties
      appState: useAppState(),
      container: 'container',
    };
  },
  methods: {
    handleResize() {
      const navBarWidth =
        this.$refs.navigationBar.getBoundingClientRect().width;

      const headerHeight = this.$refs.header.getBoundingClientRect().height;

      this.mainWidth = window.innerWidth - navBarWidth - 10;

      this.mainHeight = window.innerHeight - headerHeight - 25;

      this.appState.setAnalysisSreenDims(this.mainWidth, this.mainHeight);
    },
  },
  created() {
    // Set the time of day and update the container class accordingly
    this.timeofday = this.appState.timeOfDay;

    const analysisStore = useAnalysisStore();

    const labels = [
      'Territory Min',
      'Territory Absolute Velocity',
      'Territory Median',
      'Sector Median',
      'Sector Max',
      'Sector Min',
    ];
    labels.forEach((label) => {
      analysisStore.addNewTerritory('column1', label);
      analysisStore.addNewTerritory('column2', label);
      analysisStore.addNewTerritory('column3', label);
    });
  },
  mounted() {
    window.addEventListener('resize', this.handleResize);
    this.$refs.background.style.backgroundImage = `url('/images/${this.timeofday.toLowerCase()}.jpg')`;

    this.handleResize();

    this.analysisScreenDims = reactive(this.appState.analysisScreenDims);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
  },
};
</script>

