import macro from '@kitware/vtk.js/macros';
import vtkActor from '@kitware/vtk.js/Rendering/Core/Actor';
import vtkMapper from '@kitware/vtk.js/Rendering/Core/Mapper';
import vtkWidgetRepresentation from '@kitware/vtk.js/Widgets/Representations/WidgetRepresentation';
import vtkPolyData from '@kitware/vtk.js/Common/DataModel/PolyData';
import { RenderingTypes } from '@kitware/vtk.js/Widgets/Core/WidgetManager/Constants';
import { vec2, vec3 } from 'gl-matrix';
import { transform2dTo3d } from '@/src/core/contours/referencePlane';

const PICKING_LINE_WIDTH = 32;
const DEFAULT_LINE_WIDTH = 2;
const SELECTED_LINE_WIDTH = 4;

function vtkContourRepresentation(publicAPI, model) {
  // Set our className
  model.classHierarchy.push('vtkContourRepresentation');

  model._polyData = vtkPolyData.newInstance({ mtime: 0 });
  model._pdPoints = null;
  model._pdLines = null;

  model._pipelines = {
    lines: {
      source: publicAPI,
      mapper: vtkMapper.newInstance(),
      actor: vtkActor.newInstance({ parentProp: publicAPI }),
    },
  };

  model._pipelines.lines.actor.getProperty().setLineWidth(2);
  vtkWidgetRepresentation.connectPipeline(model._pipelines.lines);
  publicAPI.addActor(model._pipelines.lines.actor);

  publicAPI.getRepresentationStates = (input = model.inputData[0]) => input;

  function allocatePolyDataArrays(contour) {
    // reminder: polyLine is a flattened array of 2D points
    const polyLine = contour.getPolyLine();
    const refPlane = contour.getReferencePlane();
    const numPoints = contour.getPolyLineLength();
    const finalized = contour.getFinalized();
    const closedAddition = finalized ? 1 : 0;

    if (!polyLine || !refPlane) return;

    if (model._pdPoints?.length !== numPoints * 3) {
      model._pdPoints = new Float32Array(numPoints * 3);
    }
    if (model._pdLines?.length !== numPoints + 1) {
      model._pdLines = new Float32Array(numPoints + 1 + closedAddition);
    }

    let ptOffset = 0;
    let cellOffset = 0;

    model._pdLines[cellOffset++] = numPoints + closedAddition;

    const pt2d = vec2.create();
    const pt3d = vec3.create();
    for (let i = 0; i < polyLine.length; ) {
      model._pdLines[cellOffset++] = ptOffset / 3;

      vec3.set(pt2d, polyLine[i++], polyLine[i++]);
      transform2dTo3d(pt2d, refPlane, pt3d);

      /* eslint-disable prefer-destructuring */
      model._pdPoints[ptOffset++] = pt3d[0];
      model._pdPoints[ptOffset++] = pt3d[1];
      model._pdPoints[ptOffset++] = pt3d[2];
      /* eslint-enable prefer-destructuring */
    }

    if (finalized) {
      model._pdLines[cellOffset] = 0;
    }
  }

  let width = DEFAULT_LINE_WIDTH;

  publicAPI.requestData = (inData, outData) => {
    const contour = publicAPI.getRepresentationStates(inData[0]);

    allocatePolyDataArrays(contour);

    if (model._pdPoints && model._pdLines) {
      model._polyData.getPoints().setData(model._pdPoints);
      model._polyData.getLines().setData(model._pdLines);

      model._polyData.getPoints().modified();
      model._polyData.modified();
    }

    width = contour.getSelected() ? SELECTED_LINE_WIDTH : DEFAULT_LINE_WIDTH;
    model._pipelines.lines.actor.getProperty().setLineWidth(width);
    model._pipelines.lines.actor.getProperty().setColor(contour.getColor());

    outData[0] = model._polyData;
  };

  /**
   * When mousing over the line, if behavior != CONTEXT,
   * returns the parent state.
   * @param {object} prop
   * @param {number} compositeID
   * @returns {object}
   */
  publicAPI.getSelectedState = () => {
    return model.inputData[0];
  };

  const { updateActorVisibility } = publicAPI;
  publicAPI.updateActorVisibility = (
    renderingType = RenderingTypes.FRONT_BUFFER,
    ctxVisible = true,
    handleVisible = true
  ) => {
    if (renderingType === RenderingTypes.PICKING_BUFFER) {
      model._pipelines.lines.actor
        .getProperty()
        .setLineWidth(PICKING_LINE_WIDTH);
    } else {
      model._pipelines.lines.actor.getProperty().setLineWidth(width);
    }
    updateActorVisibility(renderingType, ctxVisible, handleVisible);
  };
}

// ----------------------------------------------------------------------------
// Object factory
// ----------------------------------------------------------------------------

const DEFAULT_VALUES = {};

// ----------------------------------------------------------------------------

export function extend(publicAPI, model, initialValues = {}) {
  const newDefault = { ...DEFAULT_VALUES, ...initialValues };
  vtkWidgetRepresentation.extend(publicAPI, model, newDefault);

  vtkContourRepresentation(publicAPI, model);
}

// ----------------------------------------------------------------------------

export const newInstance = macro.newInstance(
  extend,
  'vtkContourRepresentation'
);

// ----------------------------------------------------------------------------

export default { newInstance, extend };
