<template>
  <div
    class="custom-button"
    :class="{ highlighted: isActive }"
    @click="handleClick"
    @mouseover="isHovered = true"
    @focusin="isHovered = true"
    @mouseout="isHovered = false"
    @focusout="isHovered = false"
    @keydown.space.prevent="handleKeyDown"
  >
    <div class="button-content">
      <div>
        <h1 :style="{ color: stateColor }">{{ state }}</h1>
      </div>
      <div class="row-container">
        <div class="row">
          <h4>HEART RATE</h4>
          <h2>{{ heartrate }} BPM</h2>
        </div>
        <div class="row">
          <h4>DURATION</h4>
          <h2>{{ duration }} min</h2>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useActiveStudyStore } from '@/src/store/active-study';

export default {
  props: {
    id: { type: Number },
    state: { type: String },
    isActive: {
      type: Boolean,
      default: false,
    },
  },

  created() {
    const activeStore = useActiveStudyStore();
    const allStates = activeStore.sliceData[this.id - 1].stateButtons;
    const stateInfo = allStates[this.state];

    this.heartrate = stateInfo.heartrate;
    this.duration = stateInfo.duration;
    this.stateColor = stateInfo.stateColor;
  },
  data() {
    return {
      isHovered: false,
      activeStore: useActiveStudyStore(),
    };
  },
  methods: {
    handleClick() {
      this.activeStore.setCurrentState(this.id, this.state);
      this.$emit('stateChange', this.state);
    },
  },
};
</script>

