<!-- 
  DROPDOWN COMPONENT FOR © AREA 19, OCG WEB APP.
  ALLOWS USERS TO SELECT A BIOMARKER OVEYLAY
  DEVELOPPED IN MONTREAL, QC, CANADA.
  SEPTEMBER 2023.
 -->

<template>
  <div
    v-if="editable"
    class="dropdown-container"
    :class="{ active: active }"
    @keydown="handleKeyDown"
  >
    <div class="selected" @click="handleToggleContainer">
      <h5>{{ reportStore.report[id] }}</h5>
      <i class="fa-solid fa-caret-down" :class="{ flipped: active }"></i>
    </div>
    <Transition name="slide-fade">
      <div v-if="active" class="options">
        <div
          class="option"
          v-for="(marker, index) in reportStore.report.readers"
          :key="index"
          @click="handleClickedOption(marker)"
          @keydown="handleKeyDown"
        >
          <h5 class="option-text">{{ marker }}</h5>
        </div>
        <div class="option add-new">
          <div
            class="add-new-placeholder"
            @click="handleAddNew"
            v-show="!addingNew"
          >
            <i class="fa-solid fa-plus"></i>
            <h5>Add New</h5>
          </div>

          <div v-show="addingNew" class="new-reader">
            <input
              type="text"
              ref="newInput"
              class="new-reader-input"
              @keydown.stop="handleKeyDown"
              maxlength="25"
            />
            <i class="fa-solid fa-circle-plus add-button" @click="addNew"></i>
          </div>
        </div>
      </div>
    </Transition>
  </div>

  <h5 class="no-edit" v-else>{{ reportStore.report[id] }}</h5>
</template>

<script>
import { useReportStore } from "/src/store/report.js";

export default {
  data() {
    return {
      active: false,
      addingNew: false,
      reportStore: useReportStore(),
    };
  },
  mounted() {
    this.loadArray();
  },

  props: {
    id: { type: String },
    editable: { type: Boolean },
  },

  methods: {
    handleClickedOption(reader) {
      this.currentMarker = reader;
      this.reportStore.report[this.id] = reader;
      this.active = false;
      //   this.$emit("new-marker", this.biomarkers.indexOf(marker) + 1);
    },
    handleToggleContainer() {
      this.active = !this.active;
      this.addingNew = false;
    },
    handleAddNew(e) {
      this.addingNew = !this.addingNew;
      setTimeout(() => {
        this.$refs.newInput.focus();
      }, 100);
      e.preventDefault();
    },

    addNew() {
      const newReader = this.$refs.newInput.value;
      if (newReader) {
        this.reportStore.report.readers.push(newReader);
        this.reportStore.report[this.id] = newReader;
        this.active = false;
        this.addingNew = false;
        this.saveArray();
      }
    },

    handleKeyDown(e) {
      if (e.key === "Enter") {
        // console.log(e.key);
        this.addNew();
        // this.reportStore.report.readers.push("Enter");
      }
    },

    loadArray() {
      const savedArray = localStorage.getItem("readers");
      if (savedArray) {
        this.reportStore.report.readers = JSON.parse(savedArray);
      }
    },

    saveArray() {
      const targetArray = this.reportStore.report.readers;
      localStorage.setItem("readers", JSON.stringify(targetArray));
    },
  },

  //   watch: {
  //     addingNew(newValue) {
  //       if (newValue) {
  //         this.$refs.newInput.focus();
  //       }
  //     },
  //   },
};
</script>

<style scoped>
* {
  margin: 0px;
}

.no-edit {
  color: #000917;
  font-weight: bold;
  font-size: 13px;
  height: 17px;
  padding-top: 2px;
}
.dropdown-container {
  background: rgba(228, 228, 228, 0.6);
  /* border: 1px solid grey; */

  position: relative;
  cursor: pointer;

  height: 21px;
  display: flex;
  align-items: center;
  width: 160px;
  border-radius: 7px;
  transition: all 150ms;
  padding-right: 2px;
}

.selected {
  display: flex;

  align-items: center;
  width: 100%;
  height: 21px;
  padding-left: 8px;

  white-space: nowrap;
  /* background: #000917; */
}

.selected h5 {
  overflow: hidden;
  text-overflow: ellipsis;
  width: 130px;
}

.active {
  border-radius: 1px;
}

.options {
  position: absolute;
  z-index: 99;
  left: 0;
  top: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 162px;
  padding-top: 5px;
  padding-bottom: 5px;
  background: rgb(228, 228, 228);
  cursor: pointer;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  /* box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px; */
}

.option {
  width: 100%;
  display: flex;
  align-items: center;
  padding-top: 5px;
  padding-bottom: 5px;
}

.add-new-placeholder {
  width: 100%;
  display: flex;
  padding-left: 10px;
  gap: 20px;
}

.option-text {
  padding-left: 10px;
}
.new-reader {
  /* border: 1px solid black; */
  width: 150px;
  display: flex;
}
.new-reader-input {
  width: 125px;
  background: none;
  outline: none;
  border: none;
  padding-left: 10px;
}

/* we will explain what these classes do next! */
.slide-fade-enter-active {
  transition: all 0.3s ease-out;
}

.slide-fade-leave-active {
  transition: all 0.3s;
}

.slide-fade-enter-from,
.slide-fade-leave-to {
  transform: translateY(-5px);
  opacity: 0;
}

.option:hover {
  background: #79cfde98;
  transition: all 150ms;
}

.flipped {
  transform: rotate(180deg);
  transition: all 300ms ease;
}

.add-button {
  color: rgb(12, 6, 92);
}

.add-button:hover {
  color: rgb(11, 180, 6);
  transition: all 0.3s ease-out;
}
</style>
