  BY ACCESSING A DATABASE AND VALIDATING USERNAME AND PASSWORD.
  DEVELOPPED IN MONTREAL, QC, CANADA.
  JULY 2023.
 -->

<template>
  <!-- The main container with a dynamic class binding -->
  <div class="container" ref="background">
    <!-- Login form container -->
    <div class="login-form" :class="{ loaded: loaded }">
      <!-- Displaying a personalized greeting based on the time of day. 
      Time of day is calculated in script below-->
      <h2>Good {{ appState.timeOfDay }}</h2>

      <!-- Title for the login section -->
      <h4>Sign into your account</h4>

      <!-- Login form -->
      <form @submit="login">
        <!-- Label and input field for the username -->
        <label for="username"
          >User name
          <input
            type="text"
            id="username"
            v-model="username"
            @focusin="handleFocus"
            required
        /></label>
        <!-- Invalid message that appears if credentials are not found. -->
        <div class="invalidLogin" :class="{ view: invalidLogin }">
          <p>Username not found</p>
        </div>

        <!-- Label and input field for the password -->
        <label for="password"
          >Password
          <input type="password" id="password" v-model="password" required
        /></label>
        <!-- Invalid message that appears if credentials are not found. -->
        <div class="invalidLogin" :class="{ view: invalidLogin }">
          <p>Invalid Password</p>
        </div>

        <!-- Submit button to initiate the login process -->
        <button type="submit" style="align-self: center">Log In</button>

        <!-- Button to try app as a guest. (temporary, will likely be removed) -->
        <h4 style="margin-top: 40px">Don't have an account?</h4>
        <h4
          @click="signup"
          @keydown.space.prevent="handleKeyDown"
          style="color: #0085ff"
          class="sign-up-b"
        >
          Try as Guest
        </h4>
      </form>
    </div>
  </div>
</template>

<script>
// Importing libraries
import { useAppState } from '@/src/store/ocg-app-state';

import userCredentials from '../database/users.json';
// import store from '/src/store/index.js';

export default {
  data() {
    return {
      appState: useAppState(),

      invalidLogin: false,
      loaded: false,

      username: null,
      password: null,
    };
  },
  created() {
    this.timeofday = this.appState.timeOfDay; // Getting time of day.

    if (this.appState.currentUser) {
      this.$router.push('/home');
    }
  },
  mounted() {
    // Fading in the login container once mounted succesfully.
    setTimeout(() => {
      this.loaded = true;
    }, 100);
    this.$refs.background.style.backgroundImage = `url('/images/${this.timeofday.toLowerCase()}.jpg')`;
  },

  methods: {
    /**
     * Handles the login process when the login form is submitted.
     *
     * This method is triggered when the login form is submitted. It performs the
     * following steps:
     *
     * 1. Prevents the default form submission to avoid page reload.
     * 2. Checks if the entered username and password exist in the local JSON database.
     *   2.aTODO: Will change to server side database.
     * 3. If the credentials are correct:
     *    - Stores the username in the application's state.
     *    - Navigates the user to the '/home' route, indicating successful login.
     * 4. If the credentials are incorrect, displays invalid credentials in red under fields.
     *
     * @param {Event} event - The form submission event to prevent default form behavior.
     */
    login(event) {
      // Prevent default form submission to avoid page reload
      event.preventDefault();

      // Check if the entered username and password exist in the local JSON database
      const match = userCredentials.find(
        (item) =>
          item.username === this.username && item.password === this.password
      );

      if (match) {
        // If credentials are correct:
        // 1. Emit the username to the 'required' page:
        // store.commit('setUsername', this.username);
        // 2. Save the username in the browser's local storage to remember the logged-in user:
        this.appState.setUser(this.username);
        // 3. Navigate the user to the '/home' route, indicating successful login:
        this.$router.push('/home');
      } else {
        // If credentials are incorrect:
        // Handle incorrect credentials here. (Currently only showing user not found, incorrect password text).
        this.invalidLogin = true;

        setTimeout(() => {
          this.invalidLogin = false;
        }, 2000);
      }
    },

    /**
     * Handles the signup process when the signup form is submitted. (Currently disabled.)
     *
     * This method is triggered when the signup form is submitted. It performs the
     * following steps:
     *
     * 1. Prevents the default form submission to avoid page reload.
     * 2. Creates a new user object with the entered username and password.
     * 3. Adds the new user object to the local JSON database of user credentials. (eventually to a database)
     * 4. Navigates the user to the '/home' route, indicating successful signup.
     *
     *
     * @param {Event} event - The form submission event to prevent default form behavior.
     */
    signup(event) {
      // Prevent default form submission to avoid page reload
      event.preventDefault();
      this.appState.setUser('GUEST USER');

      // Create a new user object with the entered username and password
      // const newUser = {
      //   username: this.username,
      //   password: this.password,
      // };

      // Add the new user object to the local JSON database of user credentials
      // userCredentials.push(newUser);

      // Optionally, display an alert to indicate successful signup (commented out in the code)
      // alert(`Successfully added a new user! Username: ${this.username}, Password: ${this.password}`);

      // Navigate the user to the '/home' route, indicating successful signup
      this.$router.push('/home');
    },

    // Hide warning when user starts typing again.
    handleFocus() {
      this.invalidLogin = false;
    },
  },
};
</script>

