<template>
  <div ref="chart"></div>
</template>

<script>
import * as d3 from 'd3';

export default {
  props: {
    datasets: {
      type: Array,
      required: true,
    },
    width: {
      type: Number,
      default: 400,
    },
    height: {
      type: Number,
      default: 220,
    },

    yAxisLabel: {
      type: String,
      default: 'Normalized SI (RV-LV)',
    },

    visible: { type: Array },
  },
  mounted() {
    if (this.width < 0) {
      return;
    }
    this.drawChart();
  },
  watch: {
    datasets: 'drawChart',

    width: 'drawChart',

    visible: {
      handler: 'drawChart',
      deep: true,
    },
  },
  methods: {
    drawChart() {
      // Remove the old SVG element if it exists
      d3.select(this.$refs.chart).selectAll('svg').remove();

      const glowDataset = this.divideYValuesBy2(this.datasets);

      const colors = ['#ef00af', '#f0ca07', '#00efef'];

      const svg = d3
        .select(this.$refs.chart)
        .append('svg')
        .attr('width', this.width)
        .attr('height', this.height);

      const margin = { top: 20, right: 20, bottom: 30, left: 50 };
      const innerWidth = this.width - margin.left - margin.right;
      const innerHeight = this.height - margin.top - margin.bottom;

      // Calculate the midpoint of the x-axis domain
      const xMidpoint = d3.mean(this.datasets.flat(), (d) => d.x);

      const xScale = d3
        .scaleLinear()
        .domain([
          0,
          d3.max(this.datasets.flat(), (d) => d.x), // Find the maximum x value across all datasets
        ])
        .range([0, innerWidth]);

      const yScale = d3
        .scaleLinear()
        .domain([
          0,
          d3.max(this.datasets.flat(), (d) => d.y) + 0.1, // Find the maximum y value across all datasets
        ])
        .range([innerHeight, 0]);

      const line = d3
        .line()
        .x((d) => xScale(d.x))
        .y((d) => yScale(d.y));

      const g = svg
        .append('g')
        .attr('transform', `translate(${margin.left},${margin.top})`);

      // Create a filter definition for the blur effect
      const filter = svg
        .append('defs')
        .append('filter')
        .attr('id', 'lineGlow')
        .attr('x', '-50%') // Adjust the horizontal position for the filter
        .attr('y', '-50%') // Adjust the vertical position for the filter
        .attr('width', '200%') // Adjust the width for the filter
        .attr('height', '200%'); // Adjust the height for the filter

      // Add a Gaussian blur filter primitive
      filter
        .append('feGaussianBlur')
        .attr('in', 'SourceGraphic') // Apply the blur to the source graphic
        .attr('stdDeviation', '10'); // Adjust the blur intensity (increase for more glow)

      // Create a group for the lines with the glow effect
      const linesWithGlow = g.append('g').attr('filter', 'url(#lineGlow)'); // Apply the filter to the group

      // Draw lines for each dataset with the glow effect
      glowDataset.forEach((dataset, index) => {
        if (this.visible[index] > 0.2) {
          linesWithGlow
            .append('path')
            .datum(dataset)
            .attr('fill', 'none')
            .attr('stroke', colors[index])
            .attr('stroke-width', 1.2) // Adjust the line width for the glow
            .attr('d', line);
        }
      });

      // Add a text label next to the Y-axis indicating the start
      g.append('text')
        .attr('x', 10) // Adjust the position of the text
        .attr('y', -5) // Adjust the vertical position of the text
        .attr('fill', 'white')
        .attr('font-size', '12px') // Change the font size here
        .text('Systole'); // The text label you want to display

      // Draw a vertical line at the midpoint
      g.append('line')
        .attr('x1', xScale(xMidpoint))
        .attr('x2', xScale(xMidpoint))
        .attr('y1', -20)
        .attr('y2', innerHeight)
        .attr('stroke', 'white') // Set the color of the line
        .attr('stroke-width', 1); // Set the width of the line

      // Create a linear gradient definition
      const gradient = svg
        .append('defs')
        .append('linearGradient')
        .attr('id', 'midpointGradient')
        .attr('x1', '0%') // Start at 0% horizontal position
        .attr('x2', '100%') // End at 100% horizontal position
        .attr('y1', '0%') // Start at 0% vertical position
        .attr('y2', '0%'); // End at 0% vertical position

      // Add gradient stops with different opacity values
      gradient
        .append('stop')
        .attr('offset', '0%')
        .attr('stop-color', 'rgba(38, 151, 192, 0.25)') // Start color
        .attr('stop-opacity', 1); // Full opacity

      gradient
        .append('stop')
        .attr('offset', '100%')
        .attr('stop-color', 'rgba(38, 151, 192, 0.25)') // End color (same as start)
        .attr('stop-opacity', 0); // No opacity (fully transparent)

      // Add a rectangle to the background for the gradient starting from the midpoint
      svg
        .insert('rect', ':first-child') // Insert as the first child
        .attr('x', xScale(xMidpoint) + 50) // Start at the midpoint
        .attr('y', 0)
        .attr('width', innerWidth - xScale(xMidpoint)) // Cover the remaining chart
        .attr('height', innerHeight + 20) // Set the height to cover the entire chart
        .style('fill', 'url(#midpointGradient)'); // Apply the gradient

      // Add a text label horizontally on top next to the line
      g.append('text')
        .attr('x', xScale(xMidpoint) + 10) // Adjust the position of the text
        .attr('y', -5) // Adjust the vertical position of the text
        .attr('fill', 'white')
        .attr('font-size', '12px')
        .text('Diastole'); // The text label you want to display

      // Draw the X and Y axes
      g.append('g')
        .attr('transform', `translate(0,${innerHeight})`)
        .call(d3.axisBottom(xScale).ticks(4).tickSize(0)) // Set the size of the tick lines);
        .selectAll('text') // Select all text elements
        .attr('font-size', '12px');

      // Add Y-axis label
      svg
        .append('text')
        .attr('x', -margin.top - innerHeight / 2) // Adjust the position of the label
        .attr('y', 20) // Adjust the vertical position of the label
        .attr('font-size', '12px') // Change the font size here
        .attr('fill', 'white')
        .attr('text-anchor', 'middle') // Center align the text
        .attr('transform', 'rotate(-90)') // Rotate the text vertically
        .text(this.yAxisLabel); // The Y-axis label text

      g.append('g')
        .call(d3.axisLeft(yScale).ticks(6).tickSize(0))
        .selectAll('text') // Select all text elements
        .attr('font-size', '12px');

      // Draw lines for each dataset (drawn last so that they can stay on top)
      this.datasets.forEach((dataset, index) => {
        if (this.visible[index] > 0.2) {
          g.append('path')
            .datum(dataset)
            .attr('fill', 'none')
            .attr('stroke', colors[index]) // You can set colors dynamically or use predefined colors
            .attr('stroke-width', 2)
            .attr('d', line);
        }
      });

      // Hide the axis lines using CSS
      svg.selectAll('.domain').style('display', 'none');
    },

    divideYValuesBy2(data) {
      const result = [];

      data.forEach((dataSet) => {
        const newData = dataSet.map((point) => ({
          x: point.x,
          y: point.y / 2.5,
        }));
        result.push(newData);
      });

      return result;
    },
  },
};
</script>

<style scoped>
.domain {
  display: none;
}
</style>
