import { Vector3 } from '@kitware/vtk.js/types';
import { vec3 } from 'gl-matrix';

export enum Mode {
  Select = 'Select',
  Freehand = 'Freehand',
  Nudge = 'Nudge',
  PointPlacing = 'PointPlacing',
  Threshold = 'Threshold',
}

export const InteractionState = {
  Idle: 'Idle',
  Moving: 'Moving',
  FreehandDrawing: 'FreehandDrawing',
  Nudging: 'Nudging',
  PointPlacing: 'PointPlacing',
  Thresholding: 'Thresholding',
};

export function ignoreKey(e: any) {
  return e.altKey || e.controlKey || e.shiftKey;
}

export function getEventWorldPosition(model: any, callData: any) {
  const pos = model.manipulator.handleEvent(
    callData,
    model._apiSpecificRenderWindow
  );
  return pos?.length === 3 ? pos : null;
}

export function pickOrthogonalVector(vector: Vector3) {
  // if there is a zero component, skip it.
  const skipIndex = vector.findIndex((v) => v === 0) ?? 0;
  const orthogonal = vec3.create();
  // one possible orthogonal vector for [a,b,c] is [b,-a,0].
  // We compute one such vector ignoring the min index, since it can be zero.
  const c1Idx = (skipIndex + 1) % 3;
  const c2Idx = (skipIndex + 2) % 3;
  orthogonal[c1Idx] = vector[c2Idx];
  orthogonal[c2Idx] = -vector[c1Idx];
  return orthogonal;
}

/**
 * Creates an orthogonal axes from a given vector.
 * @param vector
 * @returns
 */
export function createOrthogonalAxes(vector: Vector3) {
  const up = pickOrthogonalVector(vector);
  const right = vec3.create();
  vec3.cross(right, vector, up);
  return { up, right };
}
