import { defineStore } from 'pinia';

export const useAnalysisStore = defineStore({
  id: 'analysis',
  state: () => ({
    analysisData: {
      column1: {
        viewerData: {},
        studyInfo: {
          heartrate: {
            baseline: null,
            hv: null,
            bh: null,
          },
          hrv: {
            baseline: null,
            hv: null,
            bh: null,
          },
        },
        territoryGraph: {
          activeTerritory: 'Territory Min',
          territories: [],
          graphs: {},
        },
      },
      column2: {
        viewerData: {},
        studyInfo: {
          heartrate: {
            baseline: null,
            hv: null,
            bh: null,
          },
          hrv: {
            baseline: null,
            hv: null,
            bh: null,
          },
        },
        territoryGraph: {
          activeTerritory: 'Territory Min',
          territories: [],
          graphs: {},
        },
      },
      column3: {
        viewerData: {},
        studyInfo: {
          heartrate: {
            baseline: null,
            hv: null,
            bh: null,
          },
          hrv: {
            baseline: null,
            hv: null,
            bh: null,
          },
        },
        territoryGraph: {
          activeTerritory: 'Territory Min',
          territories: [],
          graphs: {},
        },
      },
    },
  }),

  getters: {},
  actions: {
    generateRandomData(n) {
      const data = [];
      const startY = Math.random();

      for (let i = 0; i <= n; i++) {
        const x = i * 1;
        const baseY = 1 + 1 * Math.sin((3.5 * Math.PI * x) / n) + startY;
        const y = baseY + (0.1 + Math.random() * (0.8 - 0.1));

        data.push({ x, y });
      }

      // data.y = data.y.map((element) => element / Math.max(...data.y));
      return data;
    },

    addNewTerritory(id, territoryLabel, data) {
      let graphData;

      // Simple if else for demo
      if (!data) {
        graphData = [
          this.generateRandomData(15),
          this.generateRandomData(15),
          this.generateRandomData(15),
        ];
      } else {
        graphData = data;
      }
      this.analysisData[id].territoryGraph.territories.push(territoryLabel);
      this.analysisData[id].territoryGraph.graphs[territoryLabel] = {
        title: 'Normalized SI (RV-LV)',
        data: graphData,
        visibleLines: [1, 1, 1],

        territory: territoryLabel,
      };
    },
    setActiveTerritory(id, territoryLabel) {
      this.analysisData[id].territoryGraph.activeTerritory = territoryLabel;
    },

    getGraphData(id) {
      const active = this.analysisData[id].territoryGraph.activeTerritory;
      return this.analysisData[id].territoryGraph.graphs[active].data;
    },

    getActiveTerritory(id) {
      return this.analysisData[id].territoryGraph.activeTerritory;
    },

    getLabels(id) {
      return this.analysisData[id].territoryGraph.territories;
    },

    getDummyData() {
      const graphData = [
        this.generateCurveData(150),
        this.generateCurveData(150),
        this.generateCurveData(150),
      ];
      return {
        title: 'Normalized SI (RV-LV)',
        data: graphData,
        visibleLines: [1, 1, 1],
      };
    },

    generateCurveData(n) {
      const amplitude = 0.5;
      const noiseLevel = 1;
      const data = [];

      for (let i = 0; i < n; i++) {
        const x = i / n;
        // Use a sine function to create the base curve between 0 and 1
        const baseY = 0.5 + 0.5 * Math.sin(2 * Math.PI * x);
        // Add random noise to the base curve
        const noise = Math.random() * noiseLevel;
        const y = baseY + noise * amplitude;
        data.push({ x, y });
      }

      return data;
    },
  },
});
