import { createReferencePlane } from '@/src/core/contours/referencePlane';
import { Maybe } from '@/src/types';
import { defineStore } from 'pinia';
import { ContourID } from '@/src/types/contour';
import { ref } from 'vue';
import { useAnnotationTool } from './useAnnotationTool';

export enum ContourToolMode {
  Select = 'Select',
  FreehandDraw = 'FreehandDraw',
  CircleNudge = 'CircleNudge',
  PointPlacing = 'PointPlacing',
  Threshold = 'Threshold',
}

const makeContourDefaults = () => ({
  id: '' as ContourID,
  name: 'Contour',
  polyLine: [] as number[],
  // TODO this likely will share a ref
  referencePlane: createReferencePlane(),
});

const useContourStore = defineStore('contour', () => {
  // type This = ReturnType<typeof contourStore>;

  const selectedContourID = ref<Maybe<ContourID>>();
  const mode = ref<ContourToolMode>(ContourToolMode.FreehandDraw);

  // ignore serialize/deserialize fow now
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const { serialize, deserialize, ...toolStoreProps } = useAnnotationTool({
    toolDefaults: makeContourDefaults,
    initialLabels: {
      Endothelium: { color: '#008000' },
      Epithelium: { color: '#ff0000' },
    },
    newLabelDefault: { color: '#ff0000' },
  });

  function selectContour(id: Maybe<ContourID>) {
    selectedContourID.value = id;
  }

  // extend removeTool to handle selectedContourID case
  function removeTool(id: ContourID) {
    if (selectedContourID.value === id) {
      selectedContourID.value = null;
    }
    return toolStoreProps.removeTool(id);
  }

  function setMode(toolMode: ContourToolMode) {
    mode.value = toolMode;
  }

  return {
    ...toolStoreProps,
    removeTool,
    // do not support serialization for now
    serialize: () => {},
    deserialize: () => {},
    selectedContourID,
    mode,
    selectContour,
    setMode,
  };
});

export default useContourStore;
