import macro from '@kitware/vtk.js/macros';
import freehandBehavior from './behavior/freehand';
import { InteractionState, ignoreKey } from './behavior/common';
import selectBehavior from './behavior/select';
import nudgeBehavior from './behavior/nudge';
import pointPlacingBehavior from './behavior/pointPlacing';
import thresholdBehavior from './behavior/threshold';

export default function widgetBehavior(publicAPI: any, model: any) {
  model.classHierarchy.push('vtkContourWidgetProp');

  model.interactionState = InteractionState.Idle;
  model.mode = null;

  macro.event(publicAPI, model, 'RightClickEvent');

  // support setting per-view widget manipulators
  macro.setGet(publicAPI, model, ['manipulator']);
  macro.setGet(publicAPI, model, ['mode']);
  macro.setGet(publicAPI, model, ['interactionState']);

  publicAPI.setDisplayCallback = (callback: () => {}) =>
    model.representations[0].setDisplayCallback(callback);

  publicAPI.isInteractionIdle = () => {
    return publicAPI.getInteractionState() === InteractionState.Idle;
  };

  publicAPI.resetInteractionState = () => {
    publicAPI.setInteractionState(InteractionState.Idle);
  };

  publicAPI.isSelected = () => {
    return model.widgetState.getSelected();
  };

  publicAPI.handleRightButtonPress = (eventData: any) => {
    if (
      ignoreKey(eventData) ||
      model._widgetManager.getActiveWidget() !== publicAPI
    ) {
      return macro.VOID;
    }
    publicAPI.invokeRightClickEvent(eventData);
    return macro.EVENT_ABORT;
  };

  // select should be first so that contour selection works
  selectBehavior(publicAPI, model);
  freehandBehavior(publicAPI, model);
  pointPlacingBehavior(publicAPI, model);
  nudgeBehavior(publicAPI, model);
  thresholdBehavior(publicAPI, model);
}
