<template>
  <div
    class="biomarker-container"
    ref="biomarkerContainer"
    @mousewheel="scrollX"
  >
    <h4
      v-for="(label, index) in labels"
      :key="index"
      @keydown.space.prevent="handleKeyDown"
      :class="{ active: active === label }"
      @click="handleClick($event, label)"
    >
      {{ label }}
    </h4>
  </div>
</template>

<script>
import { useAnalysisStore } from '@/src/store/analysis';

export default {
  props: {
    id: { type: Number },
    textOpacity: {
      type: Number,
      default: 1,
    },
  },

  created() {
    this.analysisStore = useAnalysisStore();
    this.labels = this.analysisStore.getLabels(`column${this.id}`);
    this.active = this.analysisStore.getActiveTerritory(`column${this.id}`);
  },
  methods: {
    scrollX(e) {
      const containter = 'biomarkerContainer';
      this.$refs[containter].scrollLeft += e.deltaY / 5;
    },
    scrollXAmount(dx) {
      const containter = 'biomarkerContainer';
      this.$refs[containter].scrollTo({
        left: dx,
        behavior: 'smooth',
      });
    },
    handleClick(event, label) {
      this.active = label; // Setting active to the clicked label to highlight h4 tag

      const target = event.target.getBoundingClientRect().x;
      const startX = this.$refs.biomarkerContainer.getBoundingClientRect().x;

      this.analysisStore.setActiveTerritory(`column${this.id}`, label);
      this.scrollXAmount(
        target - startX + this.$refs.biomarkerContainer.scrollLeft
      );
    },
  },

  data() {
    return {
      active: null,
      labels: null,
      scrollAmount: 0,
    };
  },
};
</script>

<style scoped>
h4 {
  font-size: 14px;
  opacity: 0.6;
  transition: all 0.3s ease;
}

h4:hover {
  opacity: 1;
  cursor: pointer;
}

.active {
  opacity: 1;
}

.biomarker-container {
  display: flex;
  gap: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
  overflow-x: hidden;
  overflow-y: hidden;
  scrollbar-width: none; /* Hide the scrollbar in modern browsers */
  -ms-overflow-style: none;
  white-space: nowrap;
}
</style>
