    JULY 2023.
  -->
<template>
  <div class="patient-header">
    <!-- Section for displaying the column headers -->

    <div
      class="property"
      v-for="(id, tag) in patientProperties"
      :key="tag"
      @click="setSort(tag)"
      @keydown="handleKeyDown"
    >
      <h3>
        {{ id }}
      </h3>
      <i
        class="fa-solid fa-sort"
        aria-hidden="true"
        :style="{ color: sortBy == tag ? 'white' : null }"
      ></i>
    </div>
    <!-- <h3>Last Name <i class="fa fa-caret-down" aria-hidden="true"></i></h3> -->
    <!-- <h3>First Name</h3>
    <h3>Date of Birth</h3>
    <h3>Gender</h3>
    <h3>Scan Date</h3>
    <h3>Scan Location</h3> -->
  </div>

  <div v-if="loaded" class="patient-container" @mousemove="hovered = null">
    <!-- This is the container for displaying individual patient information 
      The code inside this div uses v-for directive to loop through each patient in the filteredPatients array 
      !We use filteredPatients so that user can search by criteria 
      The :key attribute is used to uniquely identify each patient element and aid in efficient rendering -->
    <div :key="patient.id" v-for="(patient, index) in filteredPatients">
      <!-- The 'Patient' component is used to display detailed information for each patient 
      The 'patient' prop is passed to the 'Patient' component to provide the patient data -->
      <PatientElement
        :patient="patient"
        @removePatient="handleRemovePatient"
        :hovered="index === hovered"
      />
    </div>
  </div>
  <div v-else>
    <LoadingScreen />
  </div>
</template>

<script>
import PatientElement from './PatientElement.vue';
// import store from '/src/store/index.js';
import LoadingScreen from './LoadingScreen.vue';

export default {
  emits: ['removePatient'],
  props: {
    patients: { type: Array },
    filter: { type: String, default: '' },
  },
  data() {
    return {
      loaded: false,
      sortBy: 'lastname',
      sortOrder: 1,
      patientProperties: {
        name: 'First Name',
        lastname: 'Last Name',
        dob: 'Date of Birth',
        gender: 'Gender',
        scandate: 'Scan Date',
        scanlocation: 'Scan Location',
      },
      filteredPatient: null,
      hovered: null,
    };
  },

  created() {
    window.addEventListener('keydown', this.handleKeyDown);
  },
  unmounted() {
    window.removeEventListener('keydown', this.handleKeyDown);
  },

  computed: {
    filteredPatients() {
      const filterString = this.filter.toLowerCase().replace(/\s/g, '');
      let filteredList = this.patients.slice(); // Create a copy of the original list

      if (filterString) {
        // Filter the list based on the filterString
        filteredList = filteredList.filter(
          (patient) =>
            patient.name.toLowerCase().includes(filterString) ||
            patient.lastname.toLowerCase().includes(filterString) ||
            patient.scanlocation.toLowerCase().includes(filterString)
        );
      }

      if (this.sortOrder) {
        const sortedPatientList = this.sortPatientsBy(
          filteredList,
          this.sortBy
        );
        return sortedPatientList;
      }
      const sortedPatientList = this.sortPatientsBy(
        filteredList,
        this.sortBy
      ).reverse();
      return sortedPatientList;
    },
  },

  methods: {
    handleRemovePatient(patient) {
      this.$emit('removePatient', patient);
    },
    setSort(property) {
      this.sortBy = property;

      if (!this.sortOrder) {
        this.sortOrder = 1;
      } else {
        this.sortOrder = 0;
      }
    },
    sortPatientsBy(patients, key) {
      // Make a shallow copy of the patients array to avoid modifying the original array
      const sortedPatients = [...patients];

      // Sort the copied array based on the specified key
      sortedPatients.sort((a, b) => {
        const valueA = a[key];
        const valueB = b[key];

        // Check if the values are date strings
        if (this.isDateString(valueA) && this.isDateString(valueB)) {
          const dateA = new Date(valueA);
          const dateB = new Date(valueB);

          if (!dateA && !dateB) {
            return dateA - dateB;
          }
        }

        // Use localeCompare for case-insensitive sorting of strings
        if (typeof valueA === 'string' && typeof valueB === 'string') {
          return valueA.localeCompare(valueB, undefined, {
            sensitivity: 'base',
          });
        }

        // For other data types or if the key doesn't exist, return 0 to keep the order unchanged
        return 0;
      });

      return sortedPatients;
    },

    isDateString(value) {
      // A simple regex pattern to check if the value is a date string
      const datePattern = /^\d{4}-\d{2}-\d{2}$/;
      return typeof value === 'string' && datePattern.test(value);
    },

    handleKeyDown(event) {
      if (
        event.key === 'ArrowDown' ||
        (event.key === 'Tab' && this.hovered < this.filteredPatients.length - 1)
      ) {
        event.preventDefault();
        this.hovered += 1;
      } else if (event.key === 'ArrowUp' && this.hovered >= 1) {
        event.preventDefault();
        this.hovered -= 1;
      } else if (event.key === 'Delete') {
        event.preventDefault();
        this.handleRemovePatient(this.filteredPatients[this.hovered]);
      }
    },
  },

  components: {
    PatientElement,
    LoadingScreen,
  },

  mounted() {
    setTimeout(() => {
      this.loaded = true;
    }, 600);
  },
};
</script>

