<script lang="ts">
import { useCurrentImage } from '@/src/composables/useCurrentImage';
import { useVTKCallback } from '@/src/composables/useVTKCallback';
import useContourStore, { ContourToolMode } from '@/src/store/tools/contours';
import { LPSAxisDir } from '@/src/types/lps';
import { updatePlaneManipulatorFor2DView } from '@/src/utils/manipulators';
import { getCSSCoordinatesFromEvent } from '@/src/utils/vtk-helpers';
import vtkContourWidget, {
  vtkContourViewWidget,
} from '@/src/vtk/ContourWidget';
import { Mode } from '@/src/vtk/ContourWidget/behavior/common';
import vtkLPSView2DProxy from '@/src/vtk/LPSView2DProxy';
import vtkWidgetManager from '@kitware/vtk.js/Widgets/Core/WidgetManager';
import vtkPlaneManipulator from '@kitware/vtk.js/Widgets/Manipulators/PlaneManipulator';
import type { vtkSubscription } from '@kitware/vtk.js/interfaces';
import {
  PropType,
  defineComponent,
  toRefs,
  computed,
  ref,
  onMounted,
  onUnmounted,
  watch,
  watchEffect,
  onBeforeUnmount,
} from 'vue';
import { getLPSAxisFromDir } from '@/src/utils/lps';
import { ContourID } from '@/src/types/contour';

const ModeMap: Record<ContourToolMode, Mode> = {
  [ContourToolMode.Select]: Mode.Select,
  [ContourToolMode.FreehandDraw]: Mode.Freehand,
  [ContourToolMode.CircleNudge]: Mode.Nudge,
  [ContourToolMode.PointPlacing]: Mode.PointPlacing,
  [ContourToolMode.Threshold]: Mode.Threshold,
};

export default defineComponent({
  name: 'ContourWidget2D',
  emits: ['finalize', 'select', 'contextmenu'],
  props: {
    contourId: {
      type: String as unknown as PropType<ContourID>,
      required: true,
    },
    widgetManager: {
      type: Object as PropType<vtkWidgetManager>,
      required: true,
    },
    viewId: {
      type: String,
      required: true,
    },
    viewDirection: {
      type: String as PropType<LPSAxisDir>,
      required: true,
    },
    currentSlice: {
      type: Number,
      required: true,
    },
    drawing: {
      type: Boolean,
      default: false,
    },
    selected: {
      type: Boolean,
      default: false,
    },
    mode: {
      type: String as PropType<ContourToolMode>,
      required: true,
    },
  },
  setup(props, { emit }) {
    const {
      viewId,
      contourId,
      widgetManager,
      viewDirection,
      currentSlice,
      drawing,
      selected: isSelected,
      mode,
    } = toRefs(props);

    const store = useContourStore();
    const contour = computed(() => store.toolByID[contourId.value]);
    const { currentImageData, currentImageID, currentImageMetadata } =
      useCurrentImage();

    const viewProxy = computed<vtkLPSView2DProxy | null>(() => {
      return store.$proxies.getView(viewId.value);
    });

    const widgetFactory = vtkContourWidget.newInstance({
      id: contourId.value,
      store,
      finalized: !drawing.value,
    });
    const widget = ref<vtkContourViewWidget | null>(null);
    const widgetState = computed(() => widget.value?.getWidgetState());

    onMounted(() => {
      widget.value = widgetManager.value.addWidget(
        widgetFactory
      ) as vtkContourViewWidget;
    });

    onUnmounted(() => {
      if (!widget.value) {
        return;
      }
      widgetManager.value.removeWidget(widget.value);
      widget.value.delete();
      widgetFactory.delete();
    });

    // --- mode --- //

    watchEffect(() => {
      const widgetMode = ModeMap[mode.value];
      widget.value?.setMode(widgetMode);
    });

    // --- reset cases --- //

    watch([currentSlice, currentImageID, widget], () => {
      widget.value?.resetInteractionState();
    });

    onBeforeUnmount(() => {
      // ensure we reset any existing interaction state prior to unmounting
      widget.value?.resetInteractionState();
    });

    // --- event handling --- //

    const onEndInteractionEvent = useVTKCallback(
      computed(() => widget.value?.onEndInteractionEvent)
    );

    onEndInteractionEvent(() => {
      if (widget.value?.getWidgetState().getFinalized()) {
        emit('finalize');
      }
    });

    // --- selection --- //

    const onSelectedEvent = useVTKCallback(
      computed(() => widget.value?.onSelectedEvent)
    );

    onSelectedEvent((selected) => {
      emit('select', selected);
    });

    // update widget state's selection state if selected prop changes
    watchEffect(() => {
      widgetState.value?.setSelected(isSelected.value);
      // renders any visual cues when selecting
      viewProxy.value?.renderLater();
    });

    // --- right click handling --- //

    let rightClickSub: vtkSubscription | null = null;

    onMounted(() => {
      if (!widget.value) {
        return;
      }
      rightClickSub = widget.value.onRightClickEvent((eventData) => {
        const coords = getCSSCoordinatesFromEvent(eventData);
        if (coords) {
          emit('contextmenu', coords);
        }
      });
    });

    onBeforeUnmount(() => {
      if (rightClickSub) {
        rightClickSub.unsubscribe();
        rightClickSub = null;
      }
    });

    // --- manipulator --- //

    const manipulator = vtkPlaneManipulator.newInstance();

    onMounted(() => {
      widget.value?.setManipulator(manipulator);
    });

    watchEffect(() => {
      updatePlaneManipulatorFor2DView(
        manipulator,
        viewDirection.value,
        currentSlice.value,
        currentImageMetadata.value
      );
    });

    // --- visibility --- //

    watch(
      () =>
        !!viewProxy.value &&
        !!widget.value &&
        contour.value?.slice === currentSlice.value,
      (visible) => {
        widget.value?.setVisibility(visible);
      },
      { immediate: true }
    );

    onMounted(() => {
      if (!widget.value) {
        return;
      }
      // hide handle visibility, but not picking visibility
      widget.value.setHandleVisibility(false);
      // render picking buffer
      widgetManager.value.renderWidgets();
      // render front buffer
      viewProxy.value?.renderLater();
    });

    // --- rendering --- //

    // widgetState.getColor() is backed by contourStore.
    // Whenever the color changes, update the widget state
    // mtime and render the new color.
    watch(
      () => widget.value?.getWidgetState().getColor(),
      () => {
        widget.value?.getWidgetState().modified();
        viewProxy.value?.renderLater();
      }
    );

    // --- set widget data --- //

    const viewAxisIndex = computed(() => {
      return currentImageMetadata.value.lpsOrientation[
        getLPSAxisFromDir(viewDirection.value)
      ];
    });

    watchEffect(() => {
      const imageData = currentImageData.value;
      const sliceAxis = viewAxisIndex.value;
      const slice = currentSlice.value;
      if (widget.value && imageData) {
        widget.value.setThresholdingImageSlice(imageData, sliceAxis, slice);
      }
    });

    return () => null;
  },
});
</script>
