import { Vector2, Vector3 } from '@kitware/vtk.js/types';
import { vec2, vec3 } from 'gl-matrix';
import {
  ReferencePlane,
  transform2dTo3d,
  transform3dTo2d,
} from './referencePlane';

export interface NudgeCircle {
  center: Vector3;
  radius: number;
}

/**
 * Addition in the ring of integers modulo N.
 * @param a
 * @param b
 * @param n
 * @returns
 */
function addModuloN(a: number, b: number, n: number) {
  return (a + b) % n;
}

/* eslint-disable no-param-reassign */
function calcMidpoint(out: vec2, v1: vec2, v2: vec2) {
  out[0] = (v1[0] + v2[0]) / 2;
  out[1] = (v1[1] + v2[1]) / 2;
}
/* eslint-enable no-param-reassign */

/**
 * Nudges a contour with a circle of a given radius.
 *
 * Points that are inside the circle get nudged to the circle's border.
 *
 * @param {number[]} points 2D points in flattened array
 * @param {NudgeCircle} nudgeCircle nudging circle in 3D
 * @param refPlane reference plane
 */
function nudgePoints(
  points: number[],
  nudgeCircle: NudgeCircle,
  refPlane: ReferencePlane
) {
  const { radius, center } = nudgeCircle;
  const nudgeRadius2 = radius ** 2;
  const nudgeVec = vec3.create();
  const point: Vector3 = [0, 0, 0];
  const pt2d: Vector2 = [0, 0];
  for (let i = 0; i < points.length; i += 2) {
    vec2.set(pt2d, points[i], points[i + 1]);
    transform2dTo3d(pt2d, refPlane, point);

    const dist2 = vec3.sqrDist(point, center);
    if (dist2 >= nudgeRadius2) {
      // eslint-disable-next-line no-continue
      continue;
    }

    vec3.sub(nudgeVec, point, center);
    vec3.scale(nudgeVec, nudgeVec, Math.sqrt(nudgeRadius2 / dist2));
    vec3.add(point, center, nudgeVec);

    transform3dTo2d(point, refPlane, pt2d);

    /* eslint-disable */
    points[i] = pt2d[0];
    points[i + 1] = pt2d[1];
    /* eslint-enable */
  }
}

/**
 * Adjusts contour points to stay within a desired spacing range.
 * @param points
 * @param minPointSpacing
 * @param maxPointSpacing
 * @returns
 */
function adjustPoints(
  points: number[],
  minPointSpacing: number,
  maxPointSpacing: number
) {
  if (points.length < 2) return;

  const minSpacing2 = minPointSpacing ** 2;
  const maxSpacing2 = maxPointSpacing ** 2;

  const curVec = vec2.create();
  const nextVec = vec2.create();
  const midpoint = vec2.create();
  const intermediate = vec2.create();

  let curIdx = 0;
  let crossed = false;
  let stop = false;
  do {
    let newIdx = curIdx;
    const nextIdx = addModuloN(curIdx, 2, points.length);

    vec2.set(curVec, points[curIdx], points[curIdx + 1]);
    vec2.set(nextVec, points[nextIdx], points[nextIdx + 1]);

    const dist2 = vec2.sqrDist(curVec, nextVec);

    if (dist2 < minSpacing2) {
      // merge cur and next at the midpoint
      // TODO what to do if every point is closer than minPointSpacing?
      // It's possible to collapse into a singular point.
      calcMidpoint(midpoint, curVec, nextVec);
      points.splice(nextIdx, 2);
      points.splice(curIdx, 2);
      points.splice(curIdx, 0, ...midpoint);
    } else if (dist2 > maxSpacing2) {
      // insert new points
      const numNewPoints = Math.ceil(Math.sqrt(dist2 / maxSpacing2));
      const tdelta = 1 / (numNewPoints + 1);
      const newPoints = [];
      for (let t = tdelta; t < 1; t += tdelta) {
        vec2.lerp(intermediate, curVec, nextVec, t);
        newPoints.push(...intermediate);
      }
      // TODO nudge new points
      points.splice(nextIdx, 0, ...newPoints);
      // continue processing at nextVec
      newIdx = addModuloN(curIdx, newPoints.length + 2, points.length);
    } else {
      newIdx = addModuloN(curIdx, 2, points.length);
    }

    // We handle the [last point, first point] segment by stopping
    // after we've processed one more segment after crossing.
    stop = crossed;
    crossed = newIdx < curIdx;
    curIdx = newIdx;
  } while (!stop);
}

/**
 * Nudges a contour.
 * @param contour
 * @param nudgeCircle
 * @param minPointSpacing
 * @param maxPointSpacing
 */
export function nudgeContour(
  contour: number[],
  nudgeCircle: NudgeCircle,
  referencePlane: ReferencePlane,
  minPointSpacing = 0.0001,
  maxPointSpacing = 0.1
) {
  nudgePoints(contour, nudgeCircle, referencePlane);
  adjustPoints(contour, minPointSpacing, maxPointSpacing);
  // TODO handle intersections
}

/**
 * Finds the closest point along the contour.
 * @param contour
 * @param point
 * @returns the index of the 2-tuple in the provided contour
 */
export function closestPointAlongContour(contour: number[], point: Vector2) {
  let closestIndex = 0;
  let bestDist2 = Infinity;
  const vec = vec2.create();
  for (let i = 0; i < contour.length; i += 2) {
    vec2.set(vec, contour[i], contour[i + 1]);
    const d2 = vec2.sqrDist(vec, point);
    if (d2 < bestDist2) {
      closestIndex = i;
      bestDist2 = d2;
    }
  }

  return closestIndex;
}
