<!-- STILL UNDER WORKS-->
<template>
  <div :class="{ hide: isHidden }" class="horizontal-bar">
    <canvas
      ref="canvas"
      :style="{ cursor: canvasCursor }"
      @mousedown="startResize"
      @focusin="startResize"
      @focusout="endResize"
      @mousemove="mousemoving"
      @mouseup="endResize"
      @mouseout="endResize"
      @contextmenu.prevent="handleRightClick"
      @mousewheel.passive="this.$refs.menu.closeContextMenu"
      @dblclick="handleDblClick"
    ></canvas>
    <div v-for="(location, index) in sliceObj.excludedImages" :key="index">
      <i
        v-if="location"
        @keydown="handleKeyPress"
        @click="handleExcludeImage"
        class="fa-regular fa-circle-xmark fa-lg"
        :style="{
          position: 'absolute',
          top: '15px',
          left: location * imgWidth - 25 + 'px',
          cursor: 'pointer',
        }"
      ></i>
    </div>
  </div>

  <RightClickMenu
    ref="menu"
    :has-label="hasLabelOnClick"
    :imageIncluded="includeImage"
    @add-label="handleAddLabel"
    @remove-label="handleRemoveLabel"
    @exclude-image="handleExcludeImage"
  />
</template>

<script>
import { useActiveStudyStore } from '@/src/store/active-study';
import RightClickMenu from './RightClickMenu.vue';
import { useViewSliceStore } from '../store/view-configs/slicing';

export default {
  emits: [
    'update-slice',
    'remove-label',
    'add-label',
    'exclude-image',
    'select-image',
  ],
  props: {
    sliceID: { type: Number, required: true },
    isHidden: { type: Boolean },
    numImages: { type: Number, required: true },
    imgWidth: { type: Number, required: true },
    // sliceObj: { type: Object, required: true },
    BARHEIGHT: {
      type: Number,
      default: 284,
    },
    BARWIDTH: {
      type: Number,
      // default: 800,
      default: 800,
    },
  },
  components: {
    RightClickMenu,
  },
  data() {
    return {
      activeStore: useActiveStudyStore(),
      viewSliceStore: useViewSliceStore(),
      excludedImages: [],
      handlerBarWidth: 3,
      cardiacCyclesLabelWidth: 90,
      cardiacCyclesLabelHeight: 30,
      handleBarGrab: { width: 22, height: 22 },
      addNewLabelX: 0,
      hasLabelOnClick: false,
      includeImage: true,

      canvasCursor: 'default',
      moving: false,
      movingObj: 'none',
      movingObjs: [],
      barHeight: this.BARHEIGHT,
      startX: 0,
      barWidth: this.BARWIDTH,
      mouse: {
        x: null,
        y: null,
      },
    };
  },
  created() {
    window.addEventListener('keydown', this.keyDownHandler);
    this.sliceObj = this.activeStore.sliceData[this.sliceID - 1].sliceOBJ;
  },
  unmounted() {
    window.removeEventListener('keydown', this.keyDownHandler);
  },

  mounted() {
    this.addXLocations();
    this.drawTrimmer();
  },
  watch: {
    barWidth() {
      this.drawTrimmer();
    },
    barHeight() {
      this.drawTrimmer();
    },
  },
  methods: {
    /**
     * Draws a labeled cycle object (systole or diastole) on the canvas with specified properties.
     *
     * @param {Object} objInput - The input object containing properties of the cycle.
     * @property {number} objInput.XLocation - The X-coordinate of the cycle's location.
     * @property {string} objInput.id - The text label for the cycle.
     */
    drawCycleLabel(objInput) {
      // Extract properties from the input object
      const obj = objInput;
      const x = obj.XLocation;

      // Extract text for the label
      const text = obj.id;

      // Extract label width and canvas reference
      const width = this.cardiacCyclesLabelWidth;
      const { canvas } = this.$refs;

      // Obtain canvas 2D context
      const ctx = canvas.getContext('2d');

      // Set the fill style for the container
      ctx.fillStyle = '#00000095';

      // Define positioning and dimensions for the container
      const y = this.barHeight - 35;
      const height = 30;
      const cornerRadius = 15;

      // Begin drawing the container
      ctx.beginPath();
      ctx.moveTo(x + cornerRadius, y);
      // Draw top line with rounded corner
      ctx.lineTo(x + width - cornerRadius, y);
      ctx.arcTo(x + width, y, x + width, y + cornerRadius, cornerRadius);
      // Draw right line with rounded corner
      ctx.lineTo(x + width, y + height - cornerRadius);
      ctx.arcTo(
        x + width,
        y + height,
        x + width - cornerRadius,
        y + height,
        cornerRadius
      );
      // Draw bottom line with rounded corner
      ctx.lineTo(x + cornerRadius, y + height);
      ctx.arcTo(x, y + height, x, y + height - cornerRadius, cornerRadius);
      // Draw left line with rounded corner
      ctx.lineTo(x, y + cornerRadius);
      ctx.arcTo(x, y, x + cornerRadius, y, cornerRadius);
      ctx.closePath();

      ctx.fill(); // Fill the rectangle with a semi-transparent color
      // ctx.stroke(); // Optionally, draw the stroke

      // Add Label Text
      ctx.font = '300 18px Roboto'; // Set font for the text
      ctx.textAlign = 'center';
      ctx.textBaseline = 'middle';
      ctx.fillStyle = 'white'; // Set text color to white

      // Calculate text position
      const textX = x + width / 2;
      const textY = y + height / 2;
      ctx.fillText(text, textX, textY);

      // Calculate circle positions and properties
      const circleRadius = 2;
      const circleSpacingX = 3;
      const circleSpacingY = 2.5;
      const circleColor = 'rgba(255,255,255,0.65)';

      // Draw inner handle circles
      for (let col = 0; col < 3; col++) {
        const centerX =
          x -
          10 +
          width / 2 +
          circleSpacingX +
          col * (2 * circleRadius + circleSpacingX);
        const centerY =
          y + 5 + circleSpacingY + 10 + (2 * circleRadius + circleSpacingY);

        // Draw a circle
        ctx.beginPath();
        ctx.arc(centerX, centerY, circleRadius, 0, Math.PI * 2);
        ctx.fillStyle = circleColor;
        ctx.fill();
        ctx.closePath();
      }
    },
    /**
     * Draws bars on top and bottom add borders of a specific state.
     * @param {string} state - The state identifier for which the bars are drawn.
     * @param {string} color - The color of the bars to be drawn.
     */
    drawBars(state, color) {
      const { canvas } = this.$refs;

      // Get the 2D rendering context of the canvas.
      const ctx = canvas.getContext('2d');

      // Set the fill style for the bars.
      ctx.fillStyle = color;

      // Calculate the starting and ending X coordinates for the bars.
      const start =
        this.sliceObj.states[state].start.XLocation + this.handlerBarWidth;
      const end = this.sliceObj.states[state].end.XLocation;

      // Begin drawing the bars on the canvas.
      ctx.beginPath();

      // Draw the top line of the bars.
      ctx.moveTo(start, 0);
      ctx.lineTo(end, 0);

      // Draw the bottom line of the bars.
      ctx.moveTo(start, this.barHeight - 1);
      ctx.lineTo(end, this.barHeight - 1);

      // Set the line width and stroke style for the bars.
      ctx.lineWidth = 1.5;
      ctx.strokeStyle = color;

      // Perform the stroke to render the bars on the canvas.
      ctx.stroke();
    },
    drawTrimmer() {
      const { canvas } = this.$refs;
      const context = canvas.getContext('2d');
      const canvasWidth = this.barWidth;
      const canvasHeight = this.barHeight;
      canvas.width = canvasWidth;
      canvas.height = canvasHeight;
      context.clearRect(0, 0, canvas.width, canvas.height);

      this.drawExludeImageOverlay(context);

      Object.entries(this.sliceObj.states).forEach(([state, data]) => {
        this.drawBars(state, data.color);
        data.cardiacCycles.systoles.forEach((systole) =>
          this.drawCycleLabel(systole)
        );
        data.cardiacCycles.diastoles.forEach((diastole) =>
          this.drawCycleLabel(diastole)
        );
        this.drawStartEndHandlers(data);
      });
    },

    drawhandle(ctx, xStart, yStart, xEnd, yEnd, borderRadius, color) {
      const context = ctx;
      // Draw rounded rectangle
      context.beginPath();
      context.moveTo(xStart + borderRadius, yStart);
      context.lineTo(xEnd - borderRadius, yStart);
      context.quadraticCurveTo(xEnd, yStart, xEnd, yStart + borderRadius);
      context.lineTo(xEnd, yEnd - borderRadius);
      context.quadraticCurveTo(xEnd, yEnd, xEnd - borderRadius, yEnd);
      context.lineTo(xStart + borderRadius, yEnd);
      context.quadraticCurveTo(xStart, yEnd, xStart, yEnd - borderRadius);
      context.lineTo(xStart, yStart + borderRadius);
      context.quadraticCurveTo(xStart, yStart, xStart + borderRadius, yStart);
      context.closePath();

      context.lineWidth = 2; // Border width
      context.strokeStyle = color; // Border color
      context.stroke();

      // Fill the rectangle with a color
      context.fillStyle = color;
      context.fill();

      // Calculate circle positions and properties
      const circleRadius = 2.5;
      const circleSpacingX = 3;
      const circleSpacingY = 2.5;
      const circleColor = 'rgba(255,255,255,0.65)';

      // Draw inner handle circles
      for (let row = 0; row < 3; row++) {
        for (let col = 0; col < 2; col++) {
          const centerX =
            xStart +
            borderRadius +
            circleSpacingX +
            col * (2 * circleRadius + circleSpacingX);
          const centerY =
            yStart +
            borderRadius +
            circleSpacingY +
            10 +
            row * (2 * circleRadius + circleSpacingY);

          context.beginPath();
          context.arc(centerX, centerY, circleRadius, 0, Math.PI * 2);
          context.fillStyle = circleColor;
          context.fill();
          context.closePath();
        }
      }
    },

    drawHandleTriangle(ctx, startX, startY, width, height, direction, color) {
      const context = ctx;
      context.fillStyle = color;

      context.beginPath();

      if (direction === 'left') {
        context.moveTo(startX + width, startY);
        context.lineTo(startX, startY + height);
        context.lineTo(startX + width, startY + height);
        context.closePath();
        context.stroke();
        context.fill();

        // Calculate center of the larger triangle
        const centerX = startX + width / 2;
        const centerY = startY + height / 1.6;

        // Calculate size of the smaller triangle
        const smallTriangleSize = Math.min(width, height) * 0.3;

        // Draw circles on the corners of the smaller triangle
        const cornerRadius = 2;
        context.beginPath();

        context.arc(
          centerX + smallTriangleSize,
          centerY - smallTriangleSize / 2,
          cornerRadius,
          0,
          Math.PI * 2
        );
        context.arc(
          centerX,
          centerY + smallTriangleSize / 2,
          cornerRadius,
          0,
          Math.PI * 2
        );

        context.fillStyle = '#FFFFFF85';
        context.fill();
        context.beginPath();
        context.arc(
          centerX + smallTriangleSize,
          centerY + smallTriangleSize / 2,
          cornerRadius,
          0,
          Math.PI * 2
        );
        context.fill();
      } else {
        context.moveTo(startX, startY);
        context.lineTo(startX + width, startY + height);
        context.lineTo(startX, startY + height);
        context.closePath();
        context.stroke();
        context.fill();

        // Calculate center of the larger triangle
        const centerX = startX + width / 5;
        const centerY = startY + height / 1.6;

        // Calculate size of the smaller triangle
        const smallTriangleSize = Math.min(width, height) * 0.3;

        // Draw circles on the corners of the smaller triangle
        const cornerRadius = 2;
        context.beginPath();

        context.arc(
          centerX,
          centerY - smallTriangleSize / 2,
          cornerRadius,
          0,
          Math.PI * 2
        );
        context.arc(
          centerX,
          centerY + smallTriangleSize / 2,
          cornerRadius,
          0,
          Math.PI * 2
        );

        context.fillStyle = '#FFFFFF85';
        context.fill();
        context.beginPath();
        context.arc(
          centerX + smallTriangleSize,
          centerY + smallTriangleSize / 2,
          cornerRadius,
          0,
          Math.PI * 2
        );
        context.fill();
      }
    },

    drawStartEndHandlers(dataInput) {
      const data = dataInput;
      const { canvas } = this.$refs;
      const ctx = canvas.getContext('2d');
      const borderRadius = 5;
      const width = this.handlerBarWidth + borderRadius;
      const height = this.barHeight;
      const y = 0;
      let x = data.start.XLocation;
      data.start.XLocation = x;
      const fillColor = data.color;

      // Draw Start Handle:
      ctx.beginPath();
      ctx.moveTo(x + borderRadius, y);
      ctx.lineTo(x + width - borderRadius, y);
      ctx.lineTo(x + width - borderRadius, y + height);
      ctx.lineTo(x + borderRadius, y + height);
      ctx.arcTo(x, y + height, x, y + height - borderRadius, borderRadius);
      ctx.lineTo(x, y + borderRadius);
      ctx.arcTo(x, y, x + borderRadius, y, borderRadius);

      ctx.closePath();
      ctx.fillStyle = fillColor;
      ctx.fill();

      // this.drawhandle(
      //   ctx,
      //   x + borderRadius - 4,
      //   height / 2 - this.handleBarGrab.height,
      //   x + this.handleBarGrab.width,
      //   height / 2 + this.handleBarGrab.height,
      //   8,
      //   fillColor
      // );

      this.drawHandleTriangle(
        ctx,
        x + 1,
        this.barHeight - this.handleBarGrab.width,
        this.handleBarGrab.height,
        this.handleBarGrab.width,
        'right',
        fillColor
      );

      // Draw End Handle:
      x = data.end.XLocation;
      data.end.XLocation = x;
      x -= borderRadius;
      ctx.beginPath();
      ctx.moveTo(x + borderRadius, y);
      ctx.arcTo(x + width, y, x + width, y + borderRadius, borderRadius);
      ctx.lineTo(x + width, y + height - borderRadius);
      ctx.arcTo(
        x + width,
        y + height,
        x + width - borderRadius,
        y + height,
        borderRadius
      );
      ctx.lineTo(x + borderRadius, y + height);
      ctx.closePath();
      ctx.fillStyle = fillColor;
      ctx.fill();

      // this.drawhandle(
      //   ctx,
      //   x - this.handleBarGrab.width + borderRadius + 7,
      //   height / 2 - this.handleBarGrab.height,
      //   x + borderRadius + 4,
      //   height / 2 + this.handleBarGrab.height,
      //   8,
      //   fillColor
      // );

      this.drawHandleTriangle(
        ctx,
        x - 15,
        this.barHeight - this.handleBarGrab.width,
        this.handleBarGrab.height,
        this.handleBarGrab.width,
        'left',
        fillColor
      );
    },

    findIfLabelOnClick() {
      const phaseClick = Math.floor(this.mouse.x / this.imgWidth) + 1;
      const state = this.findStateUserClicked();
      try {
        const { cardiacCycles } = this.sliceObj.states[state];

        let foundLabel = null;
        let foundIndex = null;
        Object.keys(cardiacCycles).some((key) => {
          foundIndex = cardiacCycles[key].findIndex((labelHere) => {
            if (labelHere.phaseLocation === phaseClick) {
              foundLabel = key;
              return true;
            }
            return false;
          });
          return foundIndex !== -1;
        });

        if (foundIndex !== -1) {
          return { state, foundLabel, foundIndex };
        }
      } catch (error) {
        return 'error';
      }
      return null;
    },

    drawExludeImageOverlay(ctx) {
      const context = ctx;
      context.fillStyle = '#44444492';
      context.strokeStyle = '#70707092';

      this.sliceObj.excludedImages.forEach((imgToExclude) => {
        const startLocation = (imgToExclude - 1) * this.imgWidth;

        // Draw rounded rectangle
        context.fillRect(startLocation, 0, this.imgWidth, this.barHeight - 0.5);
        context.strokeRect(
          startLocation,
          0,
          this.imgWidth,
          this.barHeight - 0.5
        );
      });
    },

    /**
     * Returns the state at which the user clicked on based on mouse position.
     * @returns {string|null} - The name of the state or null if no state was found.
     */
    findStateUserClicked() {
      // Initialize a variable to store the found state
      let foundState = null;

      // Iterate through each state in the sliceObj.states using the 'some' method
      Object.keys(this.sliceObj.states).some((state) => {
        // Convert the phase location of the state's end to X coordinate
        const endLocation = this.sliceObj.states[state].end.XLocation;

        const startLocation = this.sliceObj.states[state].start.XLocation;

        // Check if the current mouse X position is less than or equal to the end location
        if (startLocation <= this.mouse.x && this.mouse.x <= endLocation) {
          // Set the found state and return true to break out of the 'some' loop
          foundState = state;
          return true;
        }

        // Return false to continue iterating through the states
        return false;
      });

      // Return the found state (or null if no state was found)
      return foundState;
    },

    findObjonCursor(state) {
      const stateObj = this.sliceObj.states[state];

      // const mousePhase = Math.floor(this.mouse.x / this.imgWidth) + 1;
      // TODO: incorparate a way to only look at the current mousePhase
      // for labels for faster search.

      const { start, end, cardiacCycles } = stateObj;

      if (this.checkMouseOnStart(start)) {
        return start;
      }
      if (this.checkMouseOnEnd(end)) {
        return end;
      }
      const targetSystole = this.checkAllLabels(cardiacCycles.systoles);
      if (targetSystole) {
        return targetSystole;
      }
      const targetDiastole = this.checkAllLabels(cardiacCycles.diastoles);
      if (targetDiastole) {
        return targetDiastole;
      }
      return null;
    },

    checkMouseOnStart(start) {
      const xMin = start.XLocation;
      const xMax = xMin + this.handleBarGrab.width;
      const yMin = this.barHeight - this.handleBarGrab.height;
      const yMax = this.barHeight;

      return this.checkMouseInArea(xMin, xMax, yMin, yMax);
    },

    checkMouseOnEnd(end) {
      const xMin = end.XLocation - this.handleBarGrab.width;
      const xMax = xMin + this.handleBarGrab.width;
      const yMin = this.barHeight - this.handleBarGrab.height;
      const yMax = this.barHeight;

      return this.checkMouseInArea(xMin, xMax, yMin, yMax);
    },

    checkMouseOnLabel(label) {
      const xMin = label.XLocation;
      const xMax = xMin + this.cardiacCyclesLabelWidth;
      const yMin = this.barHeight - 35;
      const yMax = yMin + this.cardiacCyclesLabelHeight;

      return this.checkMouseInArea(xMin, xMax, yMin, yMax);
    },

    checkAllLabels(cardiacCycleArray) {
      return cardiacCycleArray.find((cycle) => this.checkMouseOnLabel(cycle));
    },

    checkMouseInArea(xMin, xMax, yMin, yMax) {
      return (
        this.mouse.x >= xMin &&
        this.mouse.x <= xMax &&
        this.mouse.y >= yMin &&
        this.mouse.y <= yMax
      );
    },

    // Function called when user clicks on the video trimmer.
    startResize(event) {
      this.$refs.menu.closeContextMenu();
      // Getting user mouse position:
      this.mouse.x = event.offsetX;
      this.mouse.y = event.offsetY;
      this.startX = this.mouse.x;

      if (event.ctrlKey) {
        // The Alt key is pressed
        this.handleSelectImage(event);
        return;
      }

      const clickedState = this.findStateUserClicked();

      if (clickedState) {
        const targetObj = this.findObjonCursor(clickedState);
        if (targetObj) {
          this.moving = true;
          this.movingObj = targetObj;
        }
      }
    },

    handleNotMoving() {
      const currentState = this.findStateUserClicked();
      this.updateCursor();
      if (currentState) {
        if (this.findObjonCursor(currentState)) {
          this.updateCursor('grab');
        } else {
          this.updateCursor();
        }
      }
    },

    handleCheckUpperLowerBound(id, state, dx, lowerbound, upperbound) {
      if (id === 'start') {
        return (
          this.sliceObj.states[state].start.XLocation + dx + this.imgWidth >
            this.sliceObj.states[state].end.XLocation ||
          this.sliceObj.states[state].start.XLocation + dx <= lowerbound
        );
      }
      return (
        this.sliceObj.states[state].end.XLocation + dx - this.imgWidth - 50 <
          this.sliceObj.states[state].start.XLocation ||
        this.sliceObj.states[state].end.XLocation + dx >= upperbound
      );
    },

    mousemoving(event) {
      // Check if the cursor is inside the videotrimmer or if user is dragging:
      // Used to avoid edge cases.
      if (
        !(
          (event.offsetX >=
            this.sliceObj.states.FREEBREATHING.start.XLocation &&
            event.offsetX <=
              this.sliceObj.states.BREATHHOLD.end.XLocation + 30) ||
          this.moving
        )
      ) {
        this.updateCursor();
        return;
      }
      // If inside the videotrimmer, grab mouse position:
      this.mouse.x = event.offsetX;
      this.mouse.y = event.offsetY;

      // If the user is not dragging, simply change the cursor to the appropriate event:
      // (i.e., grab or default)
      if (!this.moving) {
        this.handleNotMoving();
        return;
      }

      // If the user is dragging an object:
      event.preventDefault();
      const xhere = event.offsetX;
      const dx = xhere - this.startX; // Could potentially add a way to reduce dx if user drags too far.

      // Check whether the object we are moving is start or end:
      if (this.movingObj.id === 'end' || this.movingObj.id === 'start') {
        // Check whether the moving object is kept between bounds.

        // Getting the state before and after  the moving state.
        const previousState = this.getPreviousKey(
          this.sliceObj.states,
          this.movingObj.state
        );

        const nextState = this.getNextKey(
          this.sliceObj.states,
          this.movingObj.state
        );

        // If the moving state is FREEBREATHING [0], then the minimum the user can drag over is 0.
        // Otherwise, the minimum will be the end of the state before current.
        const lowerbound = !previousState
          ? 0
          : this.sliceObj.states[previousState].start.XLocation + this.imgWidth;

        // // If the moving state is BREATHHOLD [2], then the maximum the user can drag over is barWidth.
        // // Otherwise, the maximum will be the start of the state after current.
        const upperbound = !nextState
          ? this.barWidth
          : this.sliceObj.states[nextState].end.XLocation - this.imgWidth;

        // Check for lowebound upperbound as well as keeping space between start and end:
        if (
          this.handleCheckUpperLowerBound(
            this.movingObj.id,
            this.movingObj.state,
            dx,
            lowerbound,
            upperbound
          )
        ) {
          return;
        }

        if (
          this.checkCollision(
            this.sliceObj.states[this.movingObj.state].cardiacCycles,
            this.movingObj,
            dx
          )
        ) {
          return;
        }

        if (this.movingObj.id === 'start' && previousState) {
          // console.log(lowerbound);
          // console.log(this.sliceObj.states[previousState].end);
          const target = this.sliceObj.states[previousState].end;

          if (
            this.checkCollision(
              this.sliceObj.states[previousState].cardiacCycles,
              target,
              dx
            )
          ) {
            return;
          }
          target.XLocation += dx;
          if (this.movingObjs.length < 2) {
            this.movingObjs.push(target);
          }
        } else if (this.movingObj.id === 'end' && nextState) {
          const target = this.sliceObj.states[nextState].start;

          if (
            this.checkCollision(
              this.sliceObj.states[nextState].cardiacCycles,
              target,
              dx
            )
          ) {
            return;
          }

          target.XLocation += dx;
          if (this.movingObjs.length < 2) {
            this.movingObjs.push(target);
          }
        }

        this.startX = xhere;
        this.movingObj.XLocation += dx;
        if (this.movingObjs.length < 2) {
          this.movingObjs.push(this.movingObj);
        }

        requestAnimationFrame(this.drawTrimmer);
        return;
      }

      // 1. Check if the object that we are moving is "current, diastole or systole"

      /* If loop checking based on moving object to prevent it from going over bounds.
            If current object is "current", refering to the current phase, then it is allowed
            to travel across the states. Otherwise, the object is bounded by its start and end.
            if object is not "current" and the
            new position of the object would be out of bounds then return. */
      if (
        this.movingObj.XLocation + dx <=
          this.sliceObj.states[this.movingObj.state].start.XLocation ||
        this.movingObj.XLocation + dx >
          this.sliceObj.states[this.movingObj.state].end.XLocation -
            this.cardiacCyclesLabelWidth
      ) {
        // In the case where the condition is not satisfied, simply update position and redraw:
      } else {
        this.movingObj.XLocation += dx;
        if (this.movingObjs.length < 1) {
          this.movingObjs.push(this.movingObj);
        }
        requestAnimationFrame(this.drawTrimmer);

        // Resetting start X to the new position so
        //  that dx can be calculated based on last mouse position.
        this.startX = xhere;
      }
      // 1.Cont. If movingObj is not "current, diastole or systole" then:
    },

    checkCollision(cardiacCycles, targetObj, dx) {
      const collision = Object.keys(cardiacCycles).some((cycleKey) => {
        const cycle = cardiacCycles[cycleKey];
        return cycle.some((targetCycle) => {
          return this.checkCollisionStartEnd(targetCycle, targetObj, dx);
        });
      });

      return collision;
    },
    checkCollisionStartEnd(obj, targetObj, dx) {
      if (targetObj.id === 'start') {
        if (dx < 0) {
          return false;
        }
        return (
          targetObj.XLocation + dx - 10 >=
          (obj.phaseLocation - 1) * this.imgWidth
        ); // - 10 is addded to give a few pizels of motion for a smoother effect
      }
      if (targetObj.id === 'end') {
        if (dx > 0) {
          return false;
        }
        return (
          targetObj.XLocation + dx + 15 <= obj.phaseLocation * this.imgWidth
        );
      }
      return false;
    },

    checksingleobj(obj) {
      return (
        this.mouse.x >= obj.location && this.mouse.x <= obj.location + obj.width
      );
    },

    snapObject() {
      if (this.movingObjs.length > 1) {
        const target = this.movingObjs[1];
        const neighbour = this.movingObjs[0];

        const neighbourDx = target.id === 'start' ? -1 : 1;

        target.phaseLocation = this.convertXLocationtoPhase(
          target.XLocation,
          target.id
        );
        target.XLocation = this.convertPhaseLocationtoX(
          target.phaseLocation,
          target.id
        );

        neighbour.phaseLocation = target.phaseLocation + neighbourDx;

        neighbour.XLocation = this.convertPhaseLocationtoX(
          neighbour.phaseLocation,
          neighbour.id
        );

        // neighbour.XLocation = target.XLocation + neighbourDx;
      } else {
        const target = this.movingObj;
        target.phaseLocation = this.convertXLocationtoPhase(
          target.XLocation,
          target.id
        );
        target.XLocation = this.convertPhaseLocationtoX(
          target.phaseLocation,
          target.id
        );
      }

      this.movingObjs = [];
    },

    endResize() {
      if (!this.moving) {
        return;
      }
      this.moving = false;
      this.snapObject();
      this.drawTrimmer();

      this.startX = this.mouse.x;
      this.moving = false;
      this.movingObj = 'none';

      this.$emit('update-slice', this.sliceObj);
    },
    updateCursor(curtype = 'default') {
      this.canvasCursor = curtype;
    },
    /**
     * Retrieves the key that comes after the given state in the object.
     * @param {Object} obj - The object from which to retrieve the next key.
     * @param {string} currentKey - The state to find the next state for.
     * @returns {(string|boolean)} - The next state in the object, or false if not found or state is the last state.
     */
    getNextKey(obj, currentKey) {
      const keys = Object.keys(obj);
      const currentIndex = keys.indexOf(currentKey);

      if (currentIndex === -1 || currentIndex === keys.length - 1) {
        // If the current state is not found or it's the last state in the object
        return false;
      }

      return keys[currentIndex + 1];
    },

    /**
     * Retrieves the state that comes before the given state in the object.
     * @param {Object} obj - The object from which to retrieve the previous state.
     * @param {string} currentKey - The state to find the previous state for.
     * @returns {(string|boolean)} - The previous state in the object, or false if not found or state is the first state.
     */
    getPreviousKey(obj, currentKey) {
      const keys = Object.keys(obj);
      const currentIndex = keys.indexOf(currentKey);

      if (currentIndex <= 0) {
        // If the current state is not found or it's the first state in the object
        return false;
      }

      return keys[currentIndex - 1];
    },
    /**
     * Handles the addition of a new label (systole, diastole)
     *
     * @param {string} id - The identifier of the label to be added ('systoles' or 'diastoles').
     */
    handleAddLabel(target) {
      // Find the state where the user clicked
      const stateHere = this.findStateUserClicked();

      // Determine the phase based on the id parameter
      const phase = target === 'systoles' ? 'Systole' : 'Diastole';

      // Calculate the phase location and XLocation
      const PhaseLocation = Math.floor(this.mouse.x / this.imgWidth) + 1; // Adding 1 since phase start at 1
      const xlocation = this.convertPhaseLocationtoX(PhaseLocation);

      // Create the new label object that will be added
      const newLabel = {
        id: phase,
        state: stateHere,
        phaseLocation: PhaseLocation,
        XLocation: xlocation,
      };

      console.log('added');

      this.activeStore.addLabel(this.sliceObj.id, stateHere, target, newLabel);

      // Redraw the trimmer component
      this.drawTrimmer();
    },

    /**
     * Handles the removal of a label from cardiac cycle.
     */
    handleRemoveLabel() {
      // Retrieve information about the label to be removed
      const { state, foundLabel, foundIndex } = this.hasLabelOnClick;

      // Identify the target array containing the label
      const targetArray = this.sliceObj.states[state].cardiacCycles[foundLabel];

      // Remove the label from the target array
      const newArray = this.removeElementAtIndex(targetArray, foundIndex);

      this.activeStore.deleteLabel(
        this.sliceObj.id,
        state,
        foundLabel,
        newArray
      );

      // Emit an event to update the removed label in the prop object
      // this.$emit('remove-label', {
      //   id: this.sliceObj.id,
      //   state,
      //   cycle: foundLabel,
      //   newArray,
      // });
      // Redraw the trimmer component
      this.drawTrimmer();
    },

    /**
     * Handles a right-click event to show context menu.
     *
     * @param {MouseEvent} event - The right-click event object.
     */
    handleRightClick(event) {
      // Set the x-coordinate where the new label will be added
      this.addNewLabelX = event.offsetX;

      // Update the mouse x-coordinate (needed to find the state at which the user clicked)
      this.mouse.x = this.addNewLabelX;

      // Check if a label is present at the clicked location
      this.hasLabelOnClick = this.findIfLabelOnClick();

      const currentPhase = Math.floor(this.mouse.x / this.imgWidth) + 1;

      this.includeImage =
        this.sliceObj.excludedImages.indexOf(currentPhase) !== -1;

      // If user clicked outside of a breathing state, then show an error:
      if (this.hasLabelOnClick === 'error') {
        this.$refs.menu.popError(event);
        return;
      }

      // Otherwise open the context menu:
      this.$refs.menu.openContextMenu(event);
    },

    handleSelectImage(event) {
      // Update the mouse x-coordinate (needed to find the state at which the user clicked)
      this.mouse.x = event.offsetX;
      const currentPhase = Math.floor(this.mouse.x / this.imgWidth) + 1;

      // Check if a label is present at the clicked location
      this.hasLabelOnClick = this.findIfLabelOnClick();

      if (this.hasLabelOnClick) {
        return;
      }
      this.activeStore.selectImage(currentPhase, this.sliceID - 1);

      // this.$emit('select-image', currentPhase);
    },

    handleExcludeImage() {
      const targetImage = Math.floor(this.mouse.x / this.imgWidth) + 1;
      this.activeStore.excludeImage(targetImage, this.sliceID - 1);
      this.activeStore.clearSelect(this.sliceID - 1);

      // Redraw the trimmer component
      this.drawTrimmer();
    },

    handleDblClick() {
      const targetImage = Math.floor(this.mouse.x / this.imgWidth) + 1;
      this.activeStore.sliceData[0].sliceOBJ.targetPhase = targetImage - 1;
      console.log(targetImage);
      console.log(this.viewSliceStore.getConfig('Coronal', 9));
      this.viewSliceStore.updateConfig('Coronal', 9, {
        slice: targetImage - 1,
      });
      // this.activeStore.qualityCheck.viewing = 'CheckContours';
    },

    /**
     * Removes an element at the specified index from the given array and returns a new array
     * without modifying old array.
     *
     * @param {Array} arr - The input array from which an element needs to be removed.
     * @param {number} index - The index of the element to be removed.
     * @returns {Array} - A new array with the specified element removed.
     * @throws {Error} - Throws an error if the index is out of the valid range of the array.
     */
    removeElementAtIndex(arr, index) {
      // Check if the index is within the valid range of the array
      if (index < 0 || index >= arr.length) {
        throw new Error('Index is out of range');
      }

      // Create a new array without modifying the original array
      const newArray = [...arr];

      // Remove the element at the given index
      newArray.splice(index, 1);

      return newArray;
    },
    /**
     * Converts a phase location to its corresponding X coordinate on the display.
     * @param {number} pLocation - The phase location value.
     * @param {string} id - Identifier indicating whether it's the 'start', 'end', or cardiac cycle.
     * @returns {number} - The X coordinate corresponding to the given phase location.
     */
    convertPhaseLocationtoX(pLocation, id) {
      let xLocation;

      // Calculate X coordinate based on the provided phase location and identifier.
      if (id === 'start') {
        // For 'start' phase, adjust X coordinate considering image width.
        xLocation = (pLocation - 1) * this.imgWidth;
      } else if (id === 'end') {
        // For 'end' phase, adjust X coordinate considering image and handler bar width.
        xLocation = pLocation * this.imgWidth - this.handlerBarWidth;
      } else {
        // For cardiac phases, calculate X coordinate with respect to image width and label width.
        const spaceX = (this.imgWidth - this.cardiacCyclesLabelWidth) / 2;
        xLocation = (pLocation - 1) * this.imgWidth + spaceX;
      }

      // Return the calculated X coordinate.
      return xLocation;
    },

    convertXLocationtoPhase(xLocation, id) {
      let pLocation;

      // Calculate X coordinate based on the provided phase location and identifier.
      if (id === 'start') {
        const tempLocation = xLocation + this.imgWidth / 2;
        // For 'start' phase, adjust X coordinate considering image width.
        pLocation = Math.floor(tempLocation / this.imgWidth) + 1;
      } else if (id === 'end') {
        // For 'end' phase, adjust X coordinate considering image and handler bar width.
        const tempLocation = xLocation - this.imgWidth / 2;
        pLocation = Math.floor(tempLocation / this.imgWidth) + 1;
      } else {
        // For cardiac phases, calculate X coordinate with respect to image width and label width.
        const labelMidwayLoc = xLocation + this.cardiacCyclesLabelWidth / 2;
        pLocation = Math.floor(labelMidwayLoc / this.imgWidth) + 1;
        // pLocation = Math.floor(tempLocation / this.imgWidth);
      }

      // Return the calculated X coordinate.
      return pLocation;
    },

    /**
     * Helper Function that adds the XLocation property to the given object based on phaseLocation and id.
     * @param {object} obj - The object to which XLocation will be added.
     * @param {string} id - The identifier for the conversion (e.g., 'start', 'end').
     */
    addXLocation(obj, id) {
      const target = obj;

      // Calculate and assign the XLocation property using phaseLocation and id.
      target.XLocation = this.convertPhaseLocationtoX(target.phaseLocation, id);
    },

    /**
     * Iterates through the states in the sliceObj and adds XLocation properties.
     * XLocation is calculated for start, end, diastole, and systole phases of cardiac cycles.
     */
    addXLocations() {
      Object.entries(this.sliceObj.states).forEach(([state]) => {
        const data = this.sliceObj.states[state];

        // Add XLocation for start and end phases.
        this.addXLocation(data.start, 'start');
        this.addXLocation(data.end, 'end');

        // Add XLocation for diastoles in cardiac cycles.
        data.cardiacCycles.diastoles.forEach((diastole) =>
          this.addXLocation(diastole)
        );

        // Add XLocation for systoles in cardiac cycles.
        data.cardiacCycles.systoles.forEach((systole) =>
          this.addXLocation(systole)
        );
      });
    },
    keyDownHandler(event) {
      if (event.key === 'Delete' && this.sliceObj.selectedImages.length) {
        this.handleExcludeImage();
      }
    },
  },
};
</script>

