.canvas {
  background: black;
}
.horizontal-bar {
  height: 60px;
  position: relative;
  transition: opacity 0.3s ease-out;
}

.horizontal-bar.hide {
  opacity: 0;
  user-select: none;
  pointer-events: none;
}
</style>
