<template>
  <div
    class="biomarkerviewer"
    :style="{ width: width + 'px', height: height + 'px' }"
  >
    <AnalysisColumn :sliceID="1" :width="510" />
    <AnalysisColumn :sliceID="2" :width="510" />
    <AnalysisColumnViewer :sliceID="3" :width="510" v-if="width > 1300" />
    <AnalysisSliceControl :sliceID="2" :width="285" />
  </div>
</template>

<script>
import AnalysisColumn from '../components/AnalysisColumn.vue';
import AnalysisColumnViewer from '../components/AnalysisColumnViewer.vue';
import AnalysisSliceControl from '../components/AnalysisSliceControl.vue';

export default {
  components: {
    AnalysisColumn,
    AnalysisSliceControl,
    AnalysisColumnViewer,
  },
  props: {
    width: { type: Number },
    height: { type: Number },
  },

  methods: {
    setColumnWidth() {
      if (this.width > 1300) {
        this.columnWidth = (this.width - 10) / 3;
      } else {
        this.columnWidth = (this.width - 10) / 2;
      }
    },
  },

  created() {
    this.setColumnWidth();
  },

  watch: {
    width() {
      this.setColumnWidth();
    },
  },
};
</script>

<style scoped>
.biomarkerviewer {
  height: 100%;
  display: flex;
  gap: 5px;
  padding: 1px;
}
</style>
