<template>
  <div class="viewer-container" ref="viewerContainer">
    <div class="header-container">
      <h5>Slice {{ sliceId }}</h5>

      <BiomarkerDropDown
        :biomarkers="[
          'Biomarker 1',
          'Biomarker 2',
          'Biomarker 3',
          'This is a long marker',
        ]"
      />

      <CheckContoursButtons :sliceID="sliceId" />
    </div>
    <VtkTwoView
      class="vtk-container"
      key="sagitalv"
      :id="id"
      :viewDirection="viewDirection"
      :viewUp="viewUp"
      ref="vtkTarget"
    />
    <ImageBarNew
      :id="sliceId"
      :maxWidth="maxWidth"
      :currentPhase="currentSlice"
      :currentState="currentState.value"
      ref="imgBar"
    />
    <div class="state-buttons">
      <StateButtons
        :id="sliceId"
        :state="'FREEBREATHING'"
        :isActive="currentState.value === 'FREEBREATHING'"
        @stateChange="handleStateChange"
      />
      <StateButtons
        :id="sliceId"
        :state="'HYPERVENTILATION'"
        :isActive="currentState.value === 'HYPERVENTILATION'"
        @stateChange="handleStateChange"
      />
      <StateButtons
        :id="sliceId"
        :state="'BREATHHOLD'"
        :isActive="currentState.value === 'BREATHHOLD'"
        @stateChange="handleStateChange"
      />
    </div>
  </div>
</template>

<script>
import { useActiveStudyStore } from '@/src/store/active-study';
import { computed } from 'vue';

import VtkTwoView from './VtkTwoView.vue';
import ImageBarNew from './ImageBarNew.vue';
import StateButtons from './StateButtons.vue';
import BiomarkerDropDown from './BiomarkerDropDown.vue';
import CheckContoursButtons from './CheckContoursButtons.vue';

export default {
  components: {
    VtkTwoView,
    ImageBarNew,
    StateButtons,
    BiomarkerDropDown,
    CheckContoursButtons,
  },
  props: {
    id: { type: String },
    viewDirection: { type: String },
    viewUp: { type: String },
    sliceId: { type: Number },
    maxWidth: { type: Number },
    currentSlice: { type: Number },
    targetValue: { type: Number },
  },
  created() {
    this.currentState = computed(
      () =>
        this.activeStudyStore.sliceData[this.sliceId - 1].stateButtons
          .currentState
    );
  },

  data() {
    return {
      activeStudyStore: useActiveStudyStore(),
    };
  },

  methods: {
    handleStateChange(newState) {
      this.$refs.imgBar.scrollToState(newState);
    },
  },

  watch: {
    currentSlice() {
      // Add action when scrolling through the slices.
      // !TODO: Need to set a naming convention.
    },
  },
};
</script>

