import { defineStore } from 'pinia';
import { useLocalStorage } from '@vueuse/core';
import { ref } from 'vue';

export const useAppState = defineStore({
  id: 'ocg-app-state',
  state: () => ({
    currentDayTime: null,
    currentBackGroundImage: null,
    analysisScreenDims: { width: null, height: null },

    currentUser: ref(useLocalStorage('currentUser', '')),
    currentView: ref(useLocalStorage('currentView', 'QualityCheck')),
  }),

  getters: {
    /**
     * Gets the time of day based on the current hour.
     *
     * This function determines the time of day (Morning, Afternoon, Evening, or Day)
     * based on the current hour of the system clock. It is used for displaying a
     * personalized greeting and adjusting the user interface based on the time of day.
     *
     * @returns {string} The time of day as a string (Morning, Afternoon, Evening, or Day).
     */
    timeOfDay() {
      // Get the current hour from the system clock
      const currentHour = new Date().getHours();

      // Check the current hour and return the corresponding time of day
      if (currentHour >= 5 && currentHour < 12) {
        return 'Morning'; // If it's between 5 AM and 12 PM, it's morning
      }
      if (currentHour >= 12 && currentHour < 18) {
        return 'Afternoon'; // If it's between 12 PM and 6 PM, it's afternoon
      }
      if (currentHour >= 18 && currentHour < 23) {
        return 'Evening'; // If it's between 6 PM and 11 PM, it's evening
      }
      return 'Day'; // Otherwise, it's day (between 11 PM and 5 AM)
    },
  },

  actions: {
    /**
     * Sets current logged in user.
     */
    setUser(user) {
      this.currentUser = user;
    },

    setView(view) {
      this.currentView = view;
    },

    setAnalysisSreenDims(width, height) {
      this.analysisScreenDims = { width, height };
    },

    logOut() {
      this.currentDayTime = null;
      this.currentBackGroundImage = null;
      this.currentUser = null;
      this.currentView = 'QualityCheck';
    },
  },
});
