<template>
  <div
    class="container"
    @dragenter="handleDragOver"
    @dragover="handleDragOver"
    @dragleave="handleDragLeave"
    @drop="handleImageDrop"
    :class="{ hover: hovering, hidden: !dragging }"
  >
    <div class="upload-text">
      <h4 style="width: 180px; text-align: center">
        Drop Excel File Here to Update Values
      </h4>
      <i class="fa-solid fa-file-arrow-down fa-2xl"></i>
      <input
        type="file"
        ref="imageLoaderInput"
        name="imageLoader"
        style="display: none"
        accept="image/jpeg, image/png, image/svg"
        @change="uploadImage"
        aria-labelledby="hiddenImageInputinReport"
        max="4MB"
      />
    </div>
  </div>
</template>

<script>
import { useReportStore } from "/src/store/report.js";

export default {
  props: {
    dragging: { type: Boolean },
  },
  data() {
    return {
      hovering: false,
      reportStore: useReportStore(),
    };
  },
  methods: {
    handleDragOver(event) {
      event.preventDefault();

      this.hovering = true;
    },

    handleDragLeave(event) {
      event.preventDefault();

      this.hovering = false;
    },

    async handleImageDrop(event) {
      this.hovering = false;
      event.preventDefault();
      const selectedFile = event.dataTransfer.files[0];

      const parsedExcel = await this.parseExcelFile(selectedFile);

      parsedExcel.forEach((segment) => {
        const segmentID = `AHA${segment.Segment}`;
        const segmentValue = segment.Value;

        this.reportStore.report[segmentID] = segmentValue;
      });
    },

    parseExcelFile(file) {
      const XLSX = require("xlsx");
      return new Promise((resolve, reject) => {
        const reader = new FileReader();

        reader.onload = (event) => {
          const data = event.target.result;
          const workbook = XLSX.read(data, { type: "array" });
          const firstSheetName = workbook.SheetNames[0];
          const worksheet = workbook.Sheets[firstSheetName];

          const parsedExcel = XLSX.utils.sheet_to_row_object_array(worksheet);

          resolve(parsedExcel);
        };

        reader.onerror = (error) => {
          reject(error);
        };

        reader.readAsArrayBuffer(file);
      });
    },
  },
};
</script>

<style scoped>
* {
  margin: 0px;
}

.container {
  position: absolute;
  z-index: 99999;

  height: 100%;
  border-radius: 10px;
  width: 100%;
  /* border: 1px dashed black; */
  pointer-events: all;
  opacity: 1;
  background: black;
  /* From https://css.glass */
  background: rgba(0, 0, 0, 0.3);

  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);

  transition: all 0.3s ease;
  border: 1px dashed rgba(255, 255, 255);
}

.hover {
  background: rgba(0, 0, 0, 0.2);
  pointer-events: all;
  border: 1px dashed black;
}

.hidden {
  pointer-events: none;
  opacity: 0;
  transition: all 0.3s ease;
}

.upload-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  gap: 30px;
  color: white;
}
</style>
