<template>
  <div class="range-slider" :style="{ width: canvasWidth + 'px' }">
    <canvas
      ref="canvas"
      @mousedown="handleMouseDown"
      @dblclick="handledblCkick"
      @mousemove="handleMouseMove"
      @mouseup="handleMouseUp"
      @mouseleave="handleMouseUp"
      @focusout="handleMouseUp"
      :style="{ cursor: canvasCursor }"
    ></canvas>
  </div>
</template>

<script>
import throttle from 'lodash.throttle';

export default {
  name: 'RangeSlider',
  props: {
    canvasWidth: { type: Number },
    scrollAmount: { type: Number },

    sliceObj: { type: Object },
    numImages: Number,
    imgWidth: Number,
  },

  data() {
    return {
      isDragging: false,
      startValue: 0,
      endValue: null,
      startDragX: null,

      // Edit Looks:
      canvasHeight: 44,
      scrollerHeight: 8,
      startEndHandlersHeight: 18,
      startEndHandlersWidth: 0.2,

      canvasCursor: 'pointer',
    };
  },

  computed: {
    sliderWidth() {
      return this.canvasWidth - 10;
    },

    totalParentWidth() {
      return this.imgWidth * this.numImages;
    },

    viewportWidth() {
      return (this.canvasWidth / this.totalParentWidth) * this.sliderWidth;
    },
  },

  mounted() {
    this.drawCanvas();
    this.throttledSliderMove = throttle(this.moveScale, 2);
  },
  methods: {
    drawCurrentView(startX, endX) {
      // Set the initial X coordinate
      const x = startX;

      // Calculate the width based on the start and end X coordinates
      const width = Math.abs(endX - startX);

      // Get the canvas element using a reference
      const { canvas } = this.$refs;

      // Get the 2D rendering context of the canvas
      const ctx = canvas.getContext('2d');

      // Set the stroke color
      ctx.strokeStyle = '#FFFFFF';

      // Set the fill color with some transparency
      ctx.fillStyle = '#FFFFFF10';

      // Set the height and Y coordinate for the shape
      const height = this.canvasHeight - 10;
      const y = (this.canvasHeight - height) / 2;

      // Set the line width
      ctx.lineWidth = 2;

      // Set the corner radius for rounded corners
      const cornerRadius = 6;

      // Begin drawing the shape
      ctx.beginPath();

      // Move to the starting point with a rounded corner
      ctx.moveTo(x + cornerRadius, y);

      // Draw a line to the next point with a rounded corner
      ctx.lineTo(x + width - cornerRadius, y);
      ctx.arcTo(x + width, y, x + width, y + cornerRadius, cornerRadius);

      // Continue drawing and adding rounded corners
      ctx.lineTo(x + width, y + height - cornerRadius);
      ctx.arcTo(
        x + width,
        y + height,
        x + width - cornerRadius,
        y + height,
        cornerRadius
      );
      ctx.lineTo(x + cornerRadius, y + height);
      ctx.arcTo(x, y + height, x, y + height - cornerRadius, cornerRadius);
      ctx.lineTo(x, y + cornerRadius);
      ctx.arcTo(x, y, x + cornerRadius, y, cornerRadius);

      // Close the path
      ctx.closePath();

      // Fill the shape with the specified color
      ctx.fill();

      // Draw the shape's outline
      ctx.stroke();
    },
    drawHandle(ctx, xStart, yStart, width, height, borderRadius, color) {
      const context = ctx;
      const xEnd = xStart + width;
      const yEnd = yStart + height;
      // Draw rounded rectangle
      context.beginPath();
      context.moveTo(xStart + borderRadius, yStart);
      context.lineTo(xEnd - borderRadius, yStart);
      context.quadraticCurveTo(xEnd, yStart, xEnd, yStart + borderRadius);
      context.lineTo(xEnd, yEnd - borderRadius);
      context.quadraticCurveTo(xEnd, yEnd, xEnd - borderRadius, yEnd);
      context.lineTo(xStart + borderRadius, yEnd);
      context.quadraticCurveTo(xStart, yEnd, xStart, yEnd - borderRadius);
      context.lineTo(xStart, yStart + borderRadius);
      context.quadraticCurveTo(xStart, yStart, xStart + borderRadius, yStart);
      context.closePath();

      context.lineWidth = 2; // Border width
      context.strokeStyle = color; // Border color
      context.stroke();

      // Fill the rectangle with a color
      context.fillStyle = color;
      context.fill();
    },
    drawSegment(startX, endX, color) {
      const { canvas } = this.$refs;
      const ctx = canvas.getContext('2d');

      const verticalSpace =
        (this.canvasHeight - this.startEndHandlersHeight) / 2;
      const distance = Math.abs(endX - startX);

      this.drawHandle(
        ctx,
        startX,
        verticalSpace,
        this.startEndHandlersWidth,
        this.startEndHandlersHeight,
        1,
        color
      );
      // Draw rest of track ahead:
      ctx.fillStyle = color;
      ctx.fillRect(
        startX,
        canvas.height / 2 - this.scrollerHeight / 2,
        distance,
        this.scrollerHeight
      );
      this.drawHandle(
        ctx,
        endX,
        verticalSpace,
        this.startEndHandlersWidth,
        this.startEndHandlersHeight,
        1,
        color
      );
    },

    drawTrack() {
      const { canvas } = this.$refs;
      const ctx = canvas.getContext('2d');
      // Draw track:
      ctx.fillStyle = '#3D3D3D';
      ctx.fillRect(
        0,
        canvas.height / 2 - this.scrollerHeight / 2,
        this.sliderWidth,
        this.scrollerHeight
      );
    },
    drawCanvas() {
      const { canvas } = this.$refs;
      const ctx = canvas.getContext('2d');

      canvas.width = this.sliderWidth;
      canvas.height = this.canvasHeight;

      const scrollToX =
        (this.scrollAmount / this.totalParentWidth) * this.sliderWidth;

      const startThumbX = Math.min(
        Math.max(scrollToX, 0.5),
        this.sliderWidth - this.viewportWidth - 0.5
      );
      const endThumbX = startThumbX + this.viewportWidth;

      this.startValue = startThumbX;
      this.endValue = endThumbX;

      const startFtoX = this.getObjLocToVal(
        this.sliceObj.states.FREEBREATHING.start
      );

      const endFtoX = this.getObjLocToVal(
        this.sliceObj.states.FREEBREATHING.end
      );

      const startHtoX = this.getObjLocToVal(
        this.sliceObj.states.HYPERVENTILATION.start
      );

      const endHtoX = this.getObjLocToVal(
        this.sliceObj.states.HYPERVENTILATION.end
      );

      const startBHtoX = this.getObjLocToVal(
        this.sliceObj.states.BREATHHOLD.start
      );

      const endBHtoX = this.getObjLocToVal(this.sliceObj.states.BREATHHOLD.end);

      ctx.clearRect(0, 0, canvas.width, canvas.height);

      // // Draw track
      this.drawTrack();

      // Draw state positions:

      this.drawSegment(startFtoX + 2, endFtoX, '#5FBBCB');
      this.drawSegment(startHtoX + 2.5, endHtoX, '#C24ABC');
      this.drawSegment(startBHtoX + 2.5, endBHtoX - 2, '#D95476');

      this.drawCurrentView(startThumbX, endThumbX);
    },

    getObjLocToVal(obj) {
      const endF = obj.XLocation / this.totalParentWidth;
      const endFtoX = endF * this.sliderWidth;

      return endFtoX;
    },

    handleMouseDown(event) {
      const { canvas } = this.$refs;
      const rect = canvas.getBoundingClientRect();
      const mouseX = event.clientX - rect.left;

      this.startDragX = mouseX;

      if (mouseX < this.endValue && mouseX > this.startValue) {
        this.isDragging = 'middle';
        this.canvasCursor = 'grab';
      }
    },
    handledblCkick(event) {
      const { canvas } = this.$refs;
      const rect = canvas.getBoundingClientRect();
      const mouseX = event.clientX - rect.left;

      const target = (mouseX - this.viewportWidth / 2) / this.sliderWidth;

      const newScroll = target * this.totalParentWidth;

      this.throttledSliderMove(newScroll);
      this.drawCanvas();
    },
    handleMouseMove(event) {
      if (!this.isDragging) {
        return;
      }

      const { canvas } = this.$refs;
      const rect = canvas.getBoundingClientRect();
      const mouseX = event.clientX - rect.left;

      const dx = mouseX - this.startDragX;

      if (this.endValue + dx > this.sliderWidth || this.startValue + dx < 0) {
        return;
      }

      const target =
        ((this.startValue + dx) / this.sliderWidth) * this.totalParentWidth;

      this.moveScale(target);

      this.startDragX = mouseX;
    },
    handleMouseUp() {
      if (!this.isDragging) {
        return;
      }
      this.isDragging = false;
      this.canvasCursor = 'pointer';
    },

    moveScale(dx) {
      this.$emit('move', dx);
    },
  },
  watch: {
    canvasWidth() {
      this.drawCanvas();
    },

    scrollAmount() {
      this.drawCanvas();
    },
  },
};
</script>

<style scoped>
.range-slider {
  position: relative;
  height: 44px;
  background: #ffffff12;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 6px;
  padding-left: 5px;
  padding-right: 5px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

canvas {
  cursor: pointer;
}
</style>
