<template>
  <div>
    <canvas ref="canvas"></canvas>
  </div>
</template>

<script>
import useContourStore from '@/src/store/tools/contours';
// import { useDICOMStore } from '@/src/store/datasets-dicom';

export default {
  mounted() {
    this.drawAllContours();
    this.mounted = true;
  },

  data() {
    return {
      contourStore: useContourStore(),
    };
  },
  props: {
    isHidden: { type: Boolean },
    numImages: { type: Number, required: true },
    imgWidth: { type: Number, required: true },
    imgHeight: { type: Number, required: true },
    volumeKey: null,
    contourIDs: null,
  },
  methods: {
    drawAllContours() {
      const { canvas } = this.$refs;
      canvas.width = this.numImages * this.imgWidth;
      canvas.height = this.imgHeight;

      // const dicomStore = useDICOMStore();
      // const image = await dicomStore.buildVolume(this.volumeKey)

      const dimensions = [192, 192];
      //

      // const contourIDs = this.contourStore.toolIDs;
      this.contourIDs.forEach((contourID) => {
        const targetArray = this.contourStore.toolByID[contourID];

        const scaledArray = this.scaleCoordinates(
          dimensions,
          targetArray.polyLine,
          [this.imgWidth, this.imgHeight]
        );

        this.drawContours(scaledArray, targetArray.slice); // l
      });
    },
    drawContours(coordinates, phase) {
      const startX = phase * this.imgWidth;

      const { canvas } = this.$refs;
      const ctx = canvas.getContext('2d');

      // Set the line color to red
      ctx.strokeStyle = 'red';

      // Set the line width (you can adjust this based on your needs)
      const lineWidth = 0.5;
      ctx.lineWidth = lineWidth;

      // Begin the path
      ctx.beginPath();

      // Move to the starting point
      const x0 = coordinates[0] + startX;
      const y0 = coordinates[1];
      ctx.moveTo(x0, y0);

      // Draw lines to the other coordinates
      for (let i = 2; i < coordinates.length; i += 2) {
        const x = coordinates[i] + startX;
        const y = coordinates[i + 1];
        ctx.lineTo(x, y);
      }

      // Close the path to create a loop (optional)
      ctx.closePath();

      // Stroke the path to draw the lines
      ctx.stroke();
    },
    scaleCoordinates(originalDimensions, coordinates, newDimensions) {
      if (coordinates.length % 2 !== 0) {
        throw new Error('Invalid input array length. It should be even.');
      }

      const [originalWidth, originalHeight] = originalDimensions;
      const [newWidth, newHeight] = newDimensions;

      const scaleFactorX = newWidth / originalWidth;
      const scaleFactorY = newHeight / originalHeight;

      const scaledCoordinates = new Array(coordinates.length);

      for (let i = 0; i < coordinates.length; i += 2) {
        const x = coordinates[i];
        const y = coordinates[i + 1];

        scaledCoordinates[i] = x * scaleFactorX;
        scaledCoordinates[i + 1] = y * scaleFactorY;
      }

      return scaledCoordinates;
    },
  },

  watch: {
    contourIDs: {
      handler() {
        if (this.mounted) {
          this.drawAllContours();
        }
      },
      deep: true, // Watch for deep changes in the array
    },
  },
};
</script>
