<!-- 
    COMPONENT THAT DISPLAYS PATIENT INFORMATION WITHIN A ROUTER LINK.
    WHEN PATIENT IS CLIKED, LOCAL STORAGE IS UPDATED TO THE CLICKED PATIENT.
    COMPONENT PART OF © AREA 19, OCG WEB APP.
    DEVELOPPED IN MONTREAL, QC, CANADA.
    JULY 2023.
  -->
<template>
  <!-- The router-link element is used to navigate to the "/analysis" route when clicked. -->
  <router-link to="/analysis" style="text-decoration: none">
    <!-- The patient information is displayed within a div with the "patient" class. -->
    <div
      class="patient"
      :class="{ containerHovered: hovered }"
      tabindex="-1"
      @mouseover="handleMouseOver"
      @focusin="handleMouseOver"
      @click="updatepatient(patient)"
      @keydown="handleKeyDown"
      @mouseout="handleMouseOut"
      @focusout="handleMouseOut"
      ref="patientElement"
    >
      <!-- The following are headings displaying various patient details. The "hovered" class is applied when the element is hovered. -->
      <h3 :class="{ hovered: isHovered }">{{ patient.name }}</h3>
      <h3 :class="{ hovered: isHovered }">{{ patient.lastname }}</h3>
      <h3 :class="{ hovered: isHovered }">{{ patient.dob }}</h3>
      <h3 :class="{ hovered: isHovered }">{{ patient.gender }}</h3>
      <h3 :class="{ hovered: isHovered }">{{ patient.scandate }}</h3>
      <h3 :class="{ hovered: isHovered }">{{ patient.scanlocation }}</h3>
    </div>
  </router-link>
</template>

<script>
// import store from '/src/store/index.js';

import { useActiveStudyStore } from '@/src/store/active-study';

export default {
  emits: ['removePatient'],
  props: {
    patient: Object,
    hovered: { type: Boolean },
  },

  created() {
    this.activeStudyStore = useActiveStudyStore();
  },
  methods: {
    // Update localStoragePatient to the patient object.
    updatepatient(patient) {
      // Use JSON.stringify to store object not string.
      // window.localStorage.setItem('currentPatient', JSON.stringify(patient));
      this.activeStudyStore.setActive(patient);
    },
    handleMouseOver() {
      this.isHovered = true;
      this.$refs.patientElement.focus();
    },

    handleMouseOut() {
      if (this.patient) {
        this.isHovered = false;
        // this.$refs.patientElement.blur();
      }
    },
    handleKeyDown(event) {
      // Code to handle delete key press
      if (event.key === 'Delete') {
        this.$emit('removePatient', this.patient);
      }
    },
  },

  data() {
    return {
      isHovered: false,
      currentPatient: '',
    };
  },
};
</script>

