  ); /* Define 6 equal-width columns using viewport width */
  color: rgb(179, 177, 177); /* Set the text color for patient information */
  align-items: center; /* Center-align content vertically within the container */
  justify-items: center; /* Center-align content horizontally within the container */
  cursor: pointer; /* Change the cursor to a pointer on hover, indicating clickability */
  transition: 0.3s; /* Apply a smooth transition effect with a duration of 0.3 seconds */
  text-decoration: none; /* Remove any text decoration (e.g., underline) from the container */
}

/* Styling when the patient information container is hovered */
.patient:hover {
  background: #229ed75e; /* Semi-transparent background color when hovered */
  /* Apply a linear gradient from transparent to a lighter blue (#229ed78a) and back to transparent horizontally */
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    #229ed78a,
    rgba(0, 0, 0, 0)
  );
  transition: 0.3s; /* Apply the same 0.3-second smooth transition effect when hovered */
}

.containerHovered {
  background: #229ed75e; /* Semi-transparent background color when hovered */
  /* Apply a linear gradient from transparent to a lighter blue (#229ed78a) and back to transparent horizontally */
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    #229ed78a,
    rgba(0, 0, 0, 0)
  );
  transition: 0.3s; /* Apply the same 0.3-second smooth transition effect when hovered */
}

/* Styling for all h3 elements within the patient container */
h3 {
  font-weight: 300; /* Set the font weight to 300 (light) for the heading elements */
  color: white; /* Set the text color for the heading elements to white */
  text-decoration: none; /* Remove any text decoration from the headings */
}

