import { vec2 } from 'gl-matrix';
import { Maybe } from '@/src/types';
import macro from '@kitware/vtk.js/macros';
import { Vector2 } from '@kitware/vtk.js/types';
import { transform3dTo2d } from '@/src/core/contours/referencePlane';
import {
  InteractionState,
  Mode,
  getEventWorldPosition,
  ignoreKey,
} from './common';

const DeselectIgnoreModes = new Set([Mode.Nudge]);

export default function selectBehavior(publicAPI: any, model: any) {
  const original = { ...publicAPI };

  let moveOrigin: Vector2 = [0, 0];
  let origPolyLine: Maybe<number[]> = null;

  macro.event(publicAPI, model, 'SelectedEvent');

  const delta = vec2.create();

  function translateContour(to: Vector2) {
    if (!origPolyLine) return;

    vec2.sub(delta, to, moveOrigin);
    const polyLine = model.widgetState.getPolyLine();
    for (let i = 0; i < polyLine.length; i += 2) {
      polyLine[i] = origPolyLine[i] + delta[0];
      polyLine[i + 1] = origPolyLine[i + 1] + delta[1];
    }
    model.widgetState.modified();
  }

  function resetModeInteraction() {
    publicAPI.setInteractionState(InteractionState.Idle);
    model._interactor.cancelAnimation(publicAPI);
    publicAPI.invokeEndInteractionEvent();
  }

  publicAPI.resetInteractionState = () => {
    if (publicAPI.getInteractionState() === InteractionState.Moving) {
      resetModeInteraction();
    }
    original.resetInteractionState();
  };

  publicAPI.handleLeftButtonPress = (callData: any) => {
    if (original.handleLeftButtonPress?.(callData) === macro.EVENT_ABORT) {
      return macro.EVENT_ABORT;
    }

    if (ignoreKey(callData)) {
      return macro.VOID;
    }

    if (model._widgetManager.getActiveWidget() !== publicAPI) {
      if (!DeselectIgnoreModes.has(publicAPI.getMode())) {
        // deselect, since we clicked on something other than the widget
        publicAPI.invokeSelectedEvent(false);
      }
      return macro.VOID;
    }

    publicAPI.invokeSelectedEvent(true);

    const worldCoords = getEventWorldPosition(model, callData);
    if (!worldCoords) {
      return macro.EVENT_ABORT;
    }

    moveOrigin = transform3dTo2d(
      worldCoords,
      model.widgetState.getReferencePlane()
    );
    origPolyLine = [...model.widgetState.getPolyLine()];

    // also enable the ability to move the contour
    publicAPI.setInteractionState(InteractionState.Moving);
    model._interactor.requestAnimation(publicAPI);
    publicAPI.invokeStartInteractionEvent();

    return macro.EVENT_ABORT;
  };

  publicAPI.handleMouseMove = (callData: any) => {
    if (original.handleMouseMove?.(callData) === macro.EVENT_ABORT) {
      return macro.EVENT_ABORT;
    }

    if (
      !publicAPI.isSelected() ||
      publicAPI.getInteractionState() !== InteractionState.Moving ||
      ignoreKey(callData)
    ) {
      return macro.VOID;
    }

    const coord = getEventWorldPosition(model, callData);
    if (coord) {
      translateContour(
        transform3dTo2d(coord, model.widgetState.getReferencePlane())
      );
      publicAPI.invokeInteractionEvent();
    }

    return macro.EVENT_ABORT;
  };

  publicAPI.handleLeftButtonRelease = (callData: any) => {
    if (original.handleLeftButtonRelease?.(callData) === macro.EVENT_ABORT) {
      return macro.EVENT_ABORT;
    }

    if (
      publicAPI.getInteractionState() !== InteractionState.Moving ||
      ignoreKey(callData)
    ) {
      return macro.VOID;
    }

    const coord = getEventWorldPosition(model, callData);
    if (coord) {
      translateContour(
        transform3dTo2d(coord, model.widgetState.getReferencePlane())
      );
    }

    resetModeInteraction();
    return macro.EVENT_ABORT;
  };
}
