import { vec3 } from 'gl-matrix';
import macro from '@kitware/vtk.js/macros';
import vtkImageData from '@kitware/vtk.js/Common/DataModel/ImageData';
import { roundVector } from '@kitware/vtk.js/Common/Core/Math';
import { Maybe } from '@/src/types';
import { Vector2, Vector3 } from '@kitware/vtk.js/types';
import {
  createBinaryMask,
  ijkToIj,
  maskToBoundaryLines,
  thresholdSlice,
} from '@/src/core/contours/threshold';
import vtkActor from '@kitware/vtk.js/Rendering/Core/Actor';
import vtkMapper from '@kitware/vtk.js/Rendering/Core/Mapper';
import vtkPolyData from '@kitware/vtk.js/Common/DataModel/PolyData';
import vtkProperty from '@kitware/vtk.js/Rendering/Core/Property';
import vtkContourLoopExtraction from '@/src/vtk/ContourLoopExtraction';
import {
  getEventWorldPosition,
  ignoreKey,
  InteractionState,
  Mode,
} from './common';
import { ContourWidgetState } from '..';
import vtkTransformPolyDataToPlane from '../../TransformPolyDataToPlane';

const MERGE_CONTOURS_KEY = 'm';
const CONVEX_HULL_CONTOURS_KEY = 'c';
const THRESHOLD_LINE_WIDTH = 2;

interface Model {
  imageData: Maybe<vtkImageData>;
  sliceAxis: 0 | 1 | 2;
  slice: number;
  maskBoundaryLines: vtkPolyData;
  // point ids refer to points from maskBoundaryLines
  loops: vtkPolyData;
  contourLoopExtraction: vtkContourLoopExtraction;
  transformPolyData: vtkTransformPolyDataToPlane;
  mergeContours: boolean;
  doConvexHull: boolean;
  contour: {
    actor: vtkActor;
    mapper: vtkMapper;
    property: vtkProperty;
  };
  widgetState: ContourWidgetState;

  [name: string]: any;
}

function createActorPipeline() {
  const actor = vtkActor.newInstance();
  const mapper = vtkMapper.newInstance();
  const property = actor.getProperty();
  actor.setMapper(mapper);
  return { actor, mapper, property };
}

export default function thresholdBehavior(publicAPI: any, model: Model) {
  const original = { ...publicAPI };

  model.imageData = null;
  model.sliceAxis = 0;
  model.slice = 0;
  model.loops = vtkPolyData.newInstance();
  model.maskBoundaryLines = vtkPolyData.newInstance();
  model.mergeContours = false;
  model.doConvexHull = false;

  model.contour = createActorPipeline();
  model.contour.property.setLineWidth(THRESHOLD_LINE_WIDTH);

  model.contourLoopExtraction = vtkContourLoopExtraction.newInstance();
  model.transformPolyData = vtkTransformPolyDataToPlane.newInstance();

  /**
   * contourExtraction produces 2D loops.
   *
   * transformPolyData produces 3D loops from contourExtraction.
   */

  model.transformPolyData.setInputConnection(
    model.contourLoopExtraction.getOutputPort()
  );

  function resetVisualizingContour() {
    model.contour.mapper.setInputData(vtkPolyData.newInstance());
  }

  function addVisualizingContour() {
    model._renderer?.addActor(model.contour.actor);
  }

  function removeVisualizingContour() {
    model._renderer?.removeActor(model.contour.actor);
  }

  function commitCurrentContours() {
    const store = model.widgetState.getStore();

    // get current contour info
    const id = model.widgetState.getId();
    const drawingContour = store.toolByID[id];

    const loops2d = model.contourLoopExtraction.getOutputData();
    const loopCells = loops2d.getLines();
    const loopPoints = loops2d.getPoints();
    const maxCellLoc = loopCells.getData().length;

    const coord = [0, 0, 0];
    for (let loc = 0; loc < maxCellLoc; ) {
      const loop = loopCells.getCell(loc) as unknown as number[];
      loc += loop.length + 1;

      const polyLine: number[] = [];
      let { length } = loop;
      // break closed contours
      if (loop[0] === loop.at(-1)) {
        length--;
      }

      for (let j = 0; j < length; j++) {
        loopPoints.getPoint(loop[j], coord);
        polyLine.push(coord[0], coord[1]);
      }

      store.addTool({
        ...drawingContour,
        polyLine,
      });
    }
  }

  // set initial empty contour data
  resetVisualizingContour();

  // cache these in the event setThresholdingParameters is called
  // while thresholding.
  let curImageData: Maybe<vtkImageData> = null;
  let maskImage: Maybe<vtkImageData> = null;
  let curSliceAxis: 0 | 1 | 2 = 0;
  let curSlice: number = 0;
  let startPointWorld: Vector3 = [0, 0, 0];
  let startPointIjk: Vector3 = [0, 0, 0];
  let startPointIj: Vector2 = [0, 0];

  function renderCurrentContours() {
    if (!curImageData) {
      return;
    }

    model.transformPolyData.setRefPlane(model.widgetState.getReferencePlane());
    const pd = model.transformPolyData.getOutputData();
    model.contour.mapper.setInputData(pd);
    model.contour.mapper.modified();
  }

  function isActiveMode() {
    return publicAPI.getMode() === Mode.Threshold;
  }

  function resetModeInteraction() {
    removeVisualizingContour();
    resetVisualizingContour();

    const circleContextState = publicAPI.getWidgetState().getCircleContext();
    circleContextState.setVisible(false);

    // Do not set this widget to be finalized, as we commit all contours separately.
    // Reason: thresholding can generate more than one contour.

    model._widgetManager.releaseFocus();
    publicAPI.setInteractionState(InteractionState.Idle);
    model._interactor.cancelAnimation(publicAPI);
    publicAPI.invokeEndInteractionEvent();
  }

  publicAPI.resetInteractionState = () => {
    if (isActiveMode() && !publicAPI.isInteractionIdle()) {
      resetModeInteraction();
    }

    original.resetInteractionState();
  };

  publicAPI.setThresholdingImageSlice = (
    imageData: vtkImageData,
    sliceAxis: 0 | 1 | 2,
    slice: number
  ) => {
    model.imageData = imageData;
    model.sliceAxis = sliceAxis;
    model.slice = Math.round(slice);
  };

  publicAPI.handleKeyPress = (callData: any) => {
    if (original.handleKeyPress?.(callData) === macro.EVENT_ABORT) {
      return macro.EVENT_ABORT;
    }

    if (
      !isActiveMode() ||
      !model.manipulator ||
      ignoreKey(callData) ||
      model.widgetState.getFinalized() ||
      publicAPI.getInteractionState() !== InteractionState.Thresholding
    ) {
      return macro.VOID;
    }

    let needsRender = false;

    if (callData.key === MERGE_CONTOURS_KEY) {
      model.mergeContours = !model.mergeContours;
      model.contourLoopExtraction.setMergeContours(model.mergeContours);
      needsRender = true;
    } else if (callData.key === CONVEX_HULL_CONTOURS_KEY) {
      model.doConvexHull = !model.doConvexHull;
      model.contourLoopExtraction.setConvexHull(model.doConvexHull);
      needsRender = true;
    }

    if (needsRender) {
      renderCurrentContours();
      publicAPI.invokeInteractionEvent();
      return macro.EVENT_ABORT;
    }

    return macro.VOID;
  };

  publicAPI.handleLeftButtonPress = (callData: any) => {
    if (original.handleLeftButtonPress?.(callData) === macro.EVENT_ABORT) {
      return macro.EVENT_ABORT;
    }

    if (!isActiveMode() || !model.manipulator || ignoreKey(callData)) {
      return macro.VOID;
    }

    if (!model.imageData || model.sliceAxis == null || model.slice == null) {
      return macro.VOID;
    }

    curImageData = model.imageData;
    curSliceAxis = model.sliceAxis;
    curSlice = model.slice;
    maskImage = createBinaryMask(curImageData!, curSliceAxis);

    // do not modify a finalized contour
    if (model.widgetState.getFinalized()) {
      return macro.VOID;
    }

    // This contour widget is passive, so if another widget
    // is active, we don't do anything.
    const activeWidget = model._widgetManager.getActiveWidget();
    if (activeWidget && activeWidget !== publicAPI) {
      return macro.VOID;
    }

    const worldCoords = getEventWorldPosition(model, callData);
    if (!worldCoords) {
      return macro.VOID;
    }

    addVisualizingContour();

    // reset mergeContours
    model.mergeContours = false;

    const indexCoords = model.imageData.worldToIndex(worldCoords) as Vector3;
    startPointWorld = worldCoords;
    startPointIjk = roundVector(indexCoords);
    startPointIj = ijkToIj(startPointIjk, curSliceAxis);

    model._widgetManager.grabFocus(publicAPI);
    publicAPI.setInteractionState(InteractionState.Thresholding);
    model.widgetState.setFinalized(false);
    model.widgetState.clearPolyLine();

    model._interactor.requestAnimation(publicAPI);
    publicAPI.invokeStartInteractionEvent();

    return macro.EVENT_ABORT;
  };

  publicAPI.handleMouseMove = (callData: any) => {
    if (original.handleMouseMove?.(callData) === macro.EVENT_ABORT) {
      return macro.EVENT_ABORT;
    }

    if (
      !isActiveMode() ||
      publicAPI.getInteractionState() !== InteractionState.Thresholding ||
      ignoreKey(callData)
    ) {
      return macro.VOID;
    }

    if (!curImageData) {
      return macro.VOID;
    }

    const coord = roundVector(
      curImageData.worldToIndex(
        getEventWorldPosition(model, callData)
      ) as Vector3
    );
    if (coord && curImageData && maskImage) {
      const ijCoord = ijkToIj(coord, curSliceAxis);
      const maxDistance = Math.abs(ijCoord[0] - startPointIj[0]);
      const threshRadius = Math.abs(ijCoord[1] - startPointIj[1]);

      const ijkDims = curImageData.getDimensions();
      const ijDims = ijkToIj(ijkDims, curSliceAxis);
      const diag = Math.sqrt(ijDims[0] ** 2 + ijkDims[1] ** 2);
      const range = curImageData.getPointData().getScalars().getRange();
      const rangeWidth = range[1] - range[0];
      const adjustedThreshold =
        (Math.min(threshRadius, diag) / diag) * rangeWidth;

      thresholdSlice(
        curImageData,
        curSliceAxis,
        curSlice,
        maskImage,
        startPointIjk,
        adjustedThreshold,
        maxDistance
      );

      model.maskBoundaryLines = maskToBoundaryLines(maskImage);

      // required for contour loop extraction
      model.maskBoundaryLines.buildLinks();

      // loops are in 2D space
      model.contourLoopExtraction.setInputData(model.maskBoundaryLines);

      renderCurrentContours();

      // draw circle context
      const normal = model.manipulator.getNormal();
      const up = model._renderer.getActiveCamera().getViewUp();
      const horiz = vec3.create();
      vec3.cross(horiz, up, normal);

      const circleContextState = model.widgetState.getCircleContext();
      circleContextState.setDirection(normal);
      circleContextState.setUp(up);
      circleContextState.setRight(horiz);
      circleContextState.setOrigin(startPointWorld);
      circleContextState.setScale3([maxDistance, threshRadius, 1]);
      circleContextState.setVisible(true);

      publicAPI.invokeInteractionEvent();
    }

    return macro.EVENT_ABORT;
  };

  publicAPI.handleLeftButtonRelease = (callData: any) => {
    if (original.handleLeftButtonRelease?.(callData) === macro.EVENT_ABORT) {
      return macro.EVENT_ABORT;
    }

    if (
      !isActiveMode() ||
      publicAPI.getInteractionState() !== InteractionState.Thresholding ||
      ignoreKey(callData)
    ) {
      return macro.VOID;
    }

    commitCurrentContours();
    resetModeInteraction();

    return macro.EVENT_ABORT;
  };
}
