import { Vector2 } from '@kitware/vtk.js/types';
import { vec2 } from 'gl-matrix';
import bspline from 'b-spline';

export interface InterpolationOptions {
  degree?: number;
  interPointSpacing?: number;
  closed?: boolean;
  clamped?: boolean;
  dt?: number;
}

/**
 * @param {number[]} points A flattened array of 2D coords.
 * @param options
 */
export function interpolateCurve(
  points: number[],
  options?: InterpolationOptions
) {
  const degree = options?.degree ?? 2;
  const interPointSpacing = options?.interPointSpacing ?? 0.1;
  const closed = options?.closed ?? false;
  const clamped = options?.clamped ?? false;

  const numOriginalPoints = points.length / 3;

  const closedCurve: Vector2[] = [];
  const prevPt = vec2.create();
  let arcLength = 0;
  for (let i = 0; i < points.length; i += 2) {
    const pt = [points[i], points[i + 1]] as Vector2;
    closedCurve.push(pt);

    if (i > 0) {
      arcLength += vec2.dist(prevPt, pt);
    }
    vec2.copy(prevPt, pt);
  }

  if (closed) {
    closedCurve.push(...closedCurve.slice(0, degree + 1));
  }

  let knots: number[] | undefined;

  if (clamped) {
    knots = [];
    const npts = closedCurve.length;
    for (let i = 0; i < npts + degree + 1; i++) {
      knots.push(i);
    }
  }

  const remapT = !clamped && closed ? 1 - 1 / (numOriginalPoints + 1) : 1;
  const dt = options?.dt ?? interPointSpacing / arcLength;
  // TODO float32array?
  const out: number[] = [];

  for (let t = 0; t < 1; t += dt) {
    const ipt = bspline(t * remapT, degree, closedCurve, knots);
    out.push(ipt[0], ipt[1]);
  }

  return out;
}
