      Log Out
      <!-- Text inside the button indicating that clicking it will log the user out. -->
      <i
        class="fa-solid fa-arrow-right-from-bracket"
        style="color: #ffffff"
      ></i>
      <!-- The <i> element represents an icon (arrow pointing right) displayed next to the "Log Out" text inside the button. It's styled to have white color. -->
    </button>
  </router-link>
</template>

<script>
import { useAppState } from '@/src/store/ocg-app-state';

export default {
  methods: {
    logOutUser() {
      const appState = useAppState();
      appState.logOut(); // Logs user out and clears localstorage.
      // Logs user out and clears localstorage.
      // window.localStorage.clear();
      // window.sessionStorage.clear();
      // window.localStorage.setItem('userLoggedIn', 'GUEST USER');
