<template>
  <div
    class="dropdown-container"
    @click="active = !active"
    :class="{ active: active }"
    @keydown="handleKeyDown"
  >
    <div class="selected">
      <h5>{{ currentMarker }}</h5>
      <i class="fa-solid fa-caret-down" :class="{ flipped: active }"></i>
    </div>
    <Transition name="slide-fade">
      <div v-if="active" class="options">
        <div
          class="option"
          v-for="(marker, index) in biomarkers"
          :key="index"
          @click="handleClickedOption(marker)"
          @keydown="handleKeyDown"
        >
          <h5>{{ marker }}</h5>
        </div>
      </div>
    </Transition>
  </div>
</template>

<script>
export default {
  props: {
    biomarkers: { type: Array },
  },
  data() {
    return {
      active: false,
      currentMarker: this.biomarkers[2],
    };
  },
  methods: {
    handleClickedOption(marker) {
      this.currentMarker = marker;
      this.$emit('new-marker', this.biomarkers.indexOf(marker) + 1);
    },
  },
};
</script>

