import macro from '@kitware/vtk.js/macros';
import { Vector3 } from '@kitware/vtk.js/types';
import {
  closestPointAlongContour,
  nudgeContour,
} from '@/src/core/contours/nudge';
import {
  transform2dTo3d,
  transform3dTo2d,
} from '@/src/core/contours/referencePlane';
import { vec3 } from 'gl-matrix';
import {
  InteractionState,
  Mode,
  getEventWorldPosition,
  ignoreKey,
} from './common';
import { ContourWidgetState } from '..';

// Determines the gap between the nudge circle and the contour.
const RADIUS_GAP_PERCENT = 0.95;

interface Model {
  _radius: number;
  widgetState: ContourWidgetState;

  [name: string]: any;
}

export default function nudgeBehavior(publicAPI: any, model: Model) {
  const original = { ...publicAPI };

  model._radius = 1;

  function isActiveMode() {
    return publicAPI.getMode() === Mode.Nudge;
  }

  function doNudge(center: Vector3) {
    const polyLine = model.widgetState.getPolyLine();
    const refPlane = model.widgetState.getReferencePlane();
    nudgeContour(polyLine, { center, radius: model._radius }, refPlane);
    model.widgetState.modified();
  }

  function resetModeInteraction() {
    const circleContextState = publicAPI.getWidgetState().getCircleContext();
    circleContextState.setVisible(false);

    publicAPI.setInteractionState(InteractionState.Idle);
    model._interactor.cancelAnimation(publicAPI);
    publicAPI.invokeEndInteractionEvent();
  }

  publicAPI.resetInteractionState = () => {
    if (isActiveMode() && !publicAPI.isInteractionIdle()) {
      resetModeInteraction();
    }
    original.resetInteractionState();
  };

  publicAPI.handleLeftButtonPress = (callData: any) => {
    if (original.handleLeftButtonPress?.(callData) === macro.EVENT_ABORT) {
      return macro.EVENT_ABORT;
    }

    if (
      !isActiveMode() ||
      !publicAPI.isSelected() ||
      !model.manipulator ||
      ignoreKey(callData)
    ) {
      return macro.VOID;
    }

    // This contour widget is passive, so if another widget
    // is active, we don't do anything.
    const activeWidget = model._widgetManager.getActiveWidget();
    if (activeWidget && activeWidget !== publicAPI) {
      return macro.VOID;
    }

    const worldCoords = getEventWorldPosition(model, callData);
    if (!worldCoords) {
      return macro.VOID;
    }

    // set the radius
    const { widgetState } = model;
    const polyLine = widgetState.getPolyLine();
    const refPlane = widgetState.getReferencePlane();
    const closestPtIndex = closestPointAlongContour(
      polyLine,
      transform3dTo2d(worldCoords, refPlane)
    );

    const distance = vec3.dist(
      worldCoords,
      transform2dTo3d(
        [polyLine[closestPtIndex], polyLine[closestPtIndex + 1]],
        refPlane
      )
    );
    model._radius = distance * RADIUS_GAP_PERCENT;

    // setup the nudge circle visualization
    const normal = model.manipulator.getNormal();
    const up = model._renderer.getActiveCamera().getViewUp();
    const horiz = vec3.create();
    vec3.cross(horiz, up, normal);

    const circleContextState = widgetState.getCircleContext();
    circleContextState.setDirection(normal);
    circleContextState.setUp(up);
    circleContextState.setRight(horiz);
    circleContextState.setOrigin(worldCoords);
    circleContextState.setScale3([model._radius, model._radius, 1]);
    circleContextState.setVisible(true);

    publicAPI.setInteractionState(InteractionState.Nudging);

    model._interactor.requestAnimation(publicAPI);
    publicAPI.invokeStartInteractionEvent();
    return macro.EVENT_ABORT;
  };

  publicAPI.handleMouseMove = (callData: any) => {
    if (original.handleMouseMove?.(callData) === macro.EVENT_ABORT) {
      return macro.EVENT_ABORT;
    }

    if (
      !isActiveMode() ||
      !publicAPI.isSelected() ||
      publicAPI.getInteractionState() !== InteractionState.Nudging ||
      ignoreKey(callData)
    ) {
      return macro.VOID;
    }

    const coord = getEventWorldPosition(model, callData);
    if (coord) {
      publicAPI.getWidgetState().getCircleContext().setOrigin(coord);
      doNudge(coord);
      publicAPI.invokeInteractionEvent();
    }

    return macro.EVENT_ABORT;
  };

  publicAPI.handleLeftButtonRelease = (callData: any) => {
    if (original.handleLeftButtonRelease?.(callData) === macro.EVENT_ABORT) {
      return macro.EVENT_ABORT;
    }

    if (
      !isActiveMode() ||
      !publicAPI.isSelected() ||
      publicAPI.getInteractionState() !== InteractionState.Nudging ||
      ignoreKey(callData)
    ) {
      return macro.VOID;
    }

    const coord = getEventWorldPosition(model, callData);
    if (coord) {
      doNudge(coord);
    }

    resetModeInteraction();

    return macro.EVENT_ABORT;
  };
}
