<template>
  <div
    @contextmenu.prevent="openContextMenu"
    @click="closeContextMenu"
    @mousewheel.passive="closeContextMenu"
    @keypress.prevent="handleKeyPress"
  >
    <slot></slot>
    <transition name="fade">
      <div
        v-if="showContextMenu"
        class="custom-context-menu"
        :style="{ top: `${contextMenuY}px`, left: `${contextMenuX}px` }"
      >
        <div class="window-level">
          <h5>Window and Level:</h5>
          <div class="preset-container">
            <h5 class="preset">1</h5>
            <h5 class="preset">2</h5>
            <h5 class="preset">3</h5>
            <h5 class="preset">4</h5>
            <h5 class="preset">5</h5>
            <i class="fa-solid fa-plus fa-xs preset"></i>
          </div>
        </div>
        <div class="manipulation-options">
          <div
            class="option"
            @click="addLabel('systoles')"
            @keypress="h"
            :class="{ noClick: hasLabel || imageIncluded }"
          >
            <i class="fa-solid fa-plus fa-sm" style="color: #ffffff"></i>
            <h5>Add Systole Label</h5>
          </div>

          <div
            class="option"
            @click="addLabel('diastoles')"
            @keypress="h"
            :class="{ noClick: hasLabel || imageIncluded }"
          >
            <i class="fa-solid fa-circle-plus fa-sm" style="color: #ffffff"></i>
            <h5>Add Diastole Label</h5>
          </div>

          <div
            class="option"
            @click="removeLabel()"
            @keypress="h"
            :class="{ noClick: !hasLabel }"
          >
            <i class="fa-regular fa-trash-can fa-sm" style="color: #ffffff"></i>
            <h5>Delete Label</h5>
          </div>

          <div
            class="option"
            @click="excludeImage()"
            @keypress="h"
            :class="{ noClick: imageIncluded || hasLabel }"
          >
            <i class="fa-solid fa-square-minus"></i>
            <h5>Exclude Image</h5>
          </div>

          <div
            class="option"
            @click="excludeImage()"
            @keypress="h"
            :class="{ noClick: !imageIncluded }"
          >
            <i class="fa-solid fa-square-plus"></i>
            <h5>Include Image</h5>
          </div>
        </div>
      </div>
    </transition>
    <transition name="fade">
      <div
        v-if="showError"
        class="custom-context-menu"
        :style="{ top: `${contextMenuY}px`, left: `${contextMenuX}px` }"
      >
        <div class="warning">
          <i
            class="fa-solid fa-triangle-exclamation"
            style="color: #ffcc00"
          ></i>
          <h5>Unable to edit label! Please click within a breathing state.</h5>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  props: {
    hasLabel: null,
    imageIncluded: { type: Boolean, default: true },
  },
  emits: ['add-label', 'remove-label', 'exclude-image'],
  data() {
    return {
      showContextMenu: false,
      showError: false,
      contextMenuX: 0,
      contextMenuY: 0,
    };
  },
  methods: {
    openContextMenu(event) {
      this.showContextMenu = true;
      this.contextMenuX = event.clientX;
      this.contextMenuY = event.clientY;
    },
    closeContextMenu() {
      this.showContextMenu = false;
      this.showError = false;
    },
    addLabel(option) {
      this.$emit('add-label', option);
      this.showContextMenu = false;
      setTimeout(() => {
        this.closeContextMenu();
      }, 5);
    },
    removeLabel() {
      this.$emit('remove-label');
      this.showContextMenu = false;
    },
    excludeImage() {
      this.$emit('exclude-image');
      this.showContextMenu = false;
    },

    popError(event) {
      this.showError = true;
      this.contextMenuX = event.clientX;
      this.contextMenuY = event.clientY;
    },
    handleKeyPress() {},
  },
};
</script>

