  display: flex;
  align-items: center;
  gap: 1rem;
}

.preset {
  cursor: pointer;
}

.preset:hover {
  color: #85e3f3ce;
  transition: all 0.3s ease;
}

.manipulation-options {
  display: flex;
  flex-direction: column;
  background-color: rgba(41, 42, 45, 0.95);
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  gap: 0.2rem;
  padding: 0.5rem 0rem 0.5rem 0rem;
}

.option {
  width: 100%;

  padding: 0.2rem 0.5rem 0.2rem 0.5rem;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  cursor: pointer;
}
.option:hover {
  background: #79cfde98;
  transition: all 0.3s ease;
}

.warning {
  color: #ffcc00;
  display: flex;
  gap: 0.4rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-left: 0.5rem;
  text-align: center;
}

.option.noClick {
  opacity: 0.3;
  pointer-events: none;
}
