<script>
import { useAnalysisStore } from '@/src/store/analysis';
import GraphTerritories from './GraphTerritories.vue';
import AnalysisPolarMap from './AnalysisPolarMap.vue';
// import VtkTwoView from './VtkTwoView.vue';
// import ScreenTemp from './ScreenTemp.vue';
import BiomarkerDropDown from './BiomarkerDropDown.vue';
import PolarMap from './PolarMap.vue';
// import CheckContoursButtons from './CheckContoursButtons.vue';

import AnalysisGraph from './AnalysisGraph.vue';

export default {
  components: {
    GraphTerritories,
    AnalysisGraph,
    AnalysisPolarMap,
    // VtkTwoView,
    BiomarkerDropDown,

    PolarMap,
  },
  props: {
    sliceID: { type: Number, required: true },
    width: { type: Number },
    height: { type: Number },
  },
  data() {
    return {
      loaded: true,
      analysisStore: useAnalysisStore(),
      itemHeight: 380,
      dataHeight: 120,
      resizing: false,
      startY: null,
      polarMapWidth: null,

      visible: [0.7, 0.7, 0.7],
      marker: 3,
    };
  },

  methods: {
    handleSegmentUpdate(target) {
      if (target === -1) {
        this.visible = [1, 1, 1];
      } else {
        this.visible[target] = !this.visible[target];
      }
      console.log(this.visible);
    },
    generateRandomData(n) {
      const data = [];

      for (let i = 0; i <= n; i++) {
        const x = i * 1;
        const y = Math.random(); // Random y-value between 0 and 1

        data.push({ x, y });
      }

      return data;
    },

    handleToggleMarker(itemID) {
      this.showComponent[itemID] = true;
    },

    handleNewMarker(marker) {
      this.marker = marker;
      // console.log(this.marker);
    },

    handleBeginResize(e) {
      this.resizing = true;
      this.startY = e.clientY;
      // console.log(e.clientY);
    },

    handleResizeData(e) {
      if (this.resizing) {
        const dx = this.startY - e.clientY;
        this.dataHeight -= dx;
        this.startY = e.clientY;
      }
    },

    handleStopResize() {
      this.resizing = false;
      this.startY = null;
    },
  },
};
</script>

