<template>
  <!-- Toolbar container -->
  <div class="toolbar">
    <!-- Tools section -->
    <div class="tools">
      <!-- Patient List link -->
      <router-link to="/home" style="text-decoration: none" class="normal">
        <!-- Patient List tool -->
        <NavBarButton imgSource="Patient List.svg" tag="Patient List" />
      </router-link>

      <!-- Contour Check Button -->
      <NavBarButton
        :imgSource="
          selectedTool === 'QualityCheck'
            ? 'CheckContours_Active.svg'
            : 'CheckContours.svg'
        "
        tag="Quality Check"
        :active="selectedTool === 'QualityCheck'"
        @click="selectTool('QualityCheck')"
      />

      <!-- Graphical Analysis Button -->

      <NavBarButton
        :imgSource="
          selectedTool === 'GraphicalAnalysis'
            ? 'Analysis_Active.svg'
            : 'Analysis.svg'
        "
        tag="Analysis"
        :active="selectedTool === 'GraphicalAnalysis'"
        @click="selectTool('GraphicalAnalysis')"
      />

      <!-- Report Maker Button -->

      <NavBarButton
        :imgSource="
          selectedTool === 'ReportMaker' ? 'Report_Active.svg' : 'Report.svg'
        "
        tag="Report"
        :active="selectedTool === 'ReportMaker'"
        @click="selectTool('ReportMaker')"
      />
    </div>
  </div>
</template>

<script>
import { useAppState } from '@/src/store/ocg-app-state';
import NavBarButton from './NavBarButton.vue';

export default {
  components: {
    NavBarButton,
  },
  data() {
    return {
      appState: useAppState(),
      selectedTool: null,
    };
  },
  created() {
    // window.addEventListener('keydown', this.handleKeyDown);
    this.selectedTool = this.appState.currentView;
  },
  // unmounted() {
  //   window.removeEventListener('keydown', this.handleKeyDown);
  // },
  methods: {
    // Method to select a tool.
    selectTool(tool) {
      if (this.selectedTool === tool) {
        return; // If  already selected, do nothing
      }
      this.selectedTool = tool;
      this.appState.setView(tool);
    },

    /**
     * Handles keydown events and performs actions based on the pressed key.
     *
     * @param {Event} event - The keydown event object.
     */
    handleKeyDown(event) {
      // Define a mapping of key presses to tool names
      const keyToToolMap = {
        1: 'QualityCheck',
        2: 'GraphicalAnalysis',
        3: 'ReportMaker',
        Escape: '/home',
      };

      // Check if the pressed key is in the mapping and select the corresponding tool or route
      const selectedTool = keyToToolMap[event.key];

      if (selectedTool) {
        if (event.key === 'Escape') {
          this.$router.push(selectedTool);
        } else {
          this.selectTool(selectedTool);
        }
      }
    },
  },
};
</script>

