import macro from '@kitware/vtk.js/macros';
import { Vector2 } from '@kitware/vtk.js/types';
import { transform3dTo2d } from '@/src/core/contours/referencePlane';
import {
  getEventWorldPosition,
  ignoreKey,
  InteractionState,
  Mode,
} from './common';
import { interpolateCurve } from '../interpolation';

export default function pointPlacingBehavior(publicAPI: any, model: any) {
  const original = { ...publicAPI };

  function isActiveMode() {
    return publicAPI.getMode() === Mode.PointPlacing;
  }

  function resetModeInteraction() {
    model._widgetManager.releaseFocus();
    publicAPI.setInteractionState(InteractionState.Idle);
    model._interactor.cancelAnimation(publicAPI);
    publicAPI.invokeEndInteractionEvent();
  }

  function updateLastPoint(coord: Vector2) {
    const polyLine = model.widgetState.getPolyLine();
    // assumes polyLine.length >= 2
    const last = polyLine.length - 1;
    /* eslint-disable prefer-destructuring */
    polyLine[last - 1] = coord[0];
    polyLine[last] = coord[1];
    /* eslint-enable prefer-destructuring */
    model.widgetState.modified();
  }

  function dropLastPoint() {
    const polyLine = model.widgetState.getPolyLine();
    polyLine.splice(-2, 2);
    model.widgetState.modified();
  }

  publicAPI.resetInteractionState = () => {
    if (isActiveMode() && !publicAPI.isInteractionIdle()) {
      model.widgetState.clearPolyLine();
      resetModeInteraction();
    }
    original.resetInteractionState();
  };

  publicAPI.handleLeftButtonPress = (callData: any) => {
    if (original.handleLeftButtonPress?.(callData) === macro.EVENT_ABORT) {
      return macro.EVENT_ABORT;
    }

    if (!isActiveMode() || !model.manipulator || ignoreKey(callData)) {
      return macro.VOID;
    }

    // do not modify a finalized contour
    if (model.widgetState.getFinalized()) {
      return macro.VOID;
    }

    // This contour widget is passive, so if another widget
    // is active, we don't do anything.
    const activeWidget = model._widgetManager.getActiveWidget();
    if (activeWidget && activeWidget !== publicAPI) {
      return macro.VOID;
    }

    const worldCoords = getEventWorldPosition(model, callData);
    if (!worldCoords) {
      return macro.VOID;
    }

    const point2d = transform3dTo2d(
      worldCoords,
      model.widgetState.getReferencePlane()
    );

    // we are placing a new point!
    if (publicAPI.getInteractionState() === InteractionState.PointPlacing) {
      updateLastPoint(point2d);
      model.widgetState.add2dPointToPolyLine(point2d);

      publicAPI.invokeInteractionEvent();
    } else {
      // starting a new contour
      model._widgetManager.grabFocus(publicAPI);

      publicAPI.setInteractionState(InteractionState.PointPlacing);
      model.widgetState.setFinalized(false);
      model.widgetState.clearPolyLine();
      model.widgetState.add2dPointToPolyLine(point2d);

      // add a second point, since the last point is used as the cursor handle
      model.widgetState.add2dPointToPolyLine(point2d);

      model._interactor.requestAnimation(publicAPI);
      publicAPI.invokeStartInteractionEvent();
    }

    return macro.EVENT_ABORT;
  };

  publicAPI.handleMouseMove = (callData: any) => {
    if (original.handleMouseMove?.(callData) === macro.EVENT_ABORT) {
      return macro.EVENT_ABORT;
    }

    if (
      !isActiveMode() ||
      publicAPI.getInteractionState() !== InteractionState.PointPlacing ||
      ignoreKey(callData)
    ) {
      return macro.VOID;
    }

    const coord = getEventWorldPosition(model, callData);
    if (coord) {
      const point2d = transform3dTo2d(
        coord,
        model.widgetState.getReferencePlane()
      );
      updateLastPoint(point2d);
      publicAPI.invokeInteractionEvent();
    }

    return macro.EVENT_ABORT;
  };

  publicAPI.handleRightButtonPress = (callData: any) => {
    if (original.handleRightButtonPress?.(callData) === macro.EVENT_ABORT) {
      return macro.EVENT_ABORT;
    }

    if (
      !isActiveMode() ||
      publicAPI.getInteractionState() !== InteractionState.PointPlacing ||
      ignoreKey(callData)
    ) {
      return macro.VOID;
    }

    dropLastPoint();

    if (model.widgetState.getPolyLineLength() <= 2) {
      model.widgetState.clearPolyLine();
    } else {
      // interpolate curve
      const newCurve = interpolateCurve(model.widgetState.getPolyLine(), {
        degree: 2,
        interPointSpacing: 0.01,
        closed: true,
        clamped: false,
      });
      model.widgetState.setPolyLine(newCurve);
      model.widgetState.setFinalized(true);
    }

    resetModeInteraction();
    return macro.EVENT_ABORT;
  };
}
