/* eslint-disable prefer-destructuring */
/* eslint-disable no-continue */
import vtkPolyData from '@kitware/vtk.js/Common/DataModel/PolyData';
import { vtkAlgorithm, vtkObject } from '@kitware/vtk.js/interfaces';
import macro from '@kitware/vtk.js/macro';
import { Vector2 } from '@kitware/vtk.js/types';
import hull from 'hull.js';

interface CellPoints {
  cellType: number;
  cellPointIds: number[] | null;
}

enum Dir {
  Forward = 1,
  Backward = -1,
}

interface ParameterizedPoint {
  t: number;
  ptId: number;
}

interface vtkContourLoopExtraction extends vtkObject, vtkAlgorithm {
  getConvexHull(): boolean;
  setConvexHull(ch: boolean): boolean;
  setMergeContours(merge: boolean): boolean;
  getMergeContours(): boolean;
  extractContours(input: vtkPolyData, output: vtkPolyData): void;
  computeConvexHull(input: vtkPolyData, output: vtkPolyData): void;
  requestData(inData: [vtkPolyData], outData: [vtkPolyData]): void;
  traverseLoop(
    pd: vtkPolyData,
    dir: Dir,
    startLineId: number,
    startPtId: number,
    loopPoints: ParameterizedPoint[]
  ): number;
}

interface Model {
  [key: string]: any;
}

/**
 * Implementation taken from vtkContourLoopExtraction.cxx in the VTK project.
 *
 * Added the mergeContours flag.
 */
function vtkContourLoopExtractionCtor(
  publicAPI: vtkContourLoopExtraction,
  model: Model
) {
  model.classHierarchy.push('vtkContourLoopExtraction');

  const visited = new Set<number>();

  publicAPI.traverseLoop = (pd, dir, startLineId, startPtId, loopPoints) => {
    let lineId = startLineId;
    let lastPtId = startPtId;
    let terminated = false;
    let numInserted = 0;

    while (!terminated) {
      const { cellPointIds } = pd.getCellPoints(lineId) as CellPoints;
      if (!cellPointIds) continue;

      lastPtId =
        cellPointIds[0] !== lastPtId ? cellPointIds[0] : cellPointIds[1];
      numInserted++;

      // parametric point value
      const t = dir * numInserted;
      loopPoints.push({ t, ptId: lastPtId });

      const lineCell = pd.getPointCells(lastPtId) as unknown as number[];

      if (lineCell.length !== 2 || lastPtId === startPtId) {
        // looped
        return lastPtId;
      }

      if (lineCell.length === 2) {
        // continue along loop
        lineId = lineCell[0] !== lineId ? lineCell[0] : lineCell[1];
        visited.add(lineId);
      } else {
        // empty or invalid cell
        terminated = true;
      }
    }

    return lastPtId;
  };

  publicAPI.computeConvexHull = (input, output) => {
    const inPoints = input.getPoints().getData();

    const hullInputPoints: Array<Vector2> = [];
    for (let i = 0; i < inPoints.length; i += 3) {
      hullInputPoints.push([inPoints[i], inPoints[i + 1]]);
    }

    const convexHullPoints = hull(hullInputPoints, Infinity);

    // @ts-ignore
    const outPoints = new inPoints.constructor(convexHullPoints.length * 3);
    const outLines = new Uint32Array(1 + convexHullPoints.length + 1);
    outLines[0] = convexHullPoints.length;

    for (let i = 0, outIdx = 0; i < convexHullPoints.length; i++, outIdx += 3) {
      const pt = convexHullPoints[i] as number[];
      outPoints[outIdx] = pt[0];
      outPoints[outIdx + 1] = pt[1];
      outPoints[outIdx + 2] = 0;
      outLines[i + 1] = i;
    }

    // close loop
    outLines[outLines.length - 1] = outLines[1];

    output.getPoints().setData(outPoints);
    output.getLines().setData(outLines);
  };

  publicAPI.extractContours = (input, output) => {
    const loops: Array<ParameterizedPoint[]> = [];
    visited.clear();

    const inLines = input.getLines();
    output.getPoints().setData(Float32Array.from(input.getPoints().getData()));

    // TODO skip if cached input mtime hasn't changed.
    // iterate over input lines
    for (let li = 0; li < inLines.getNumberOfCells(); li++) {
      if (visited.has(li)) {
        continue;
      }

      const { cellPointIds } = input.getCellPoints(li) as CellPoints;
      if (!cellPointIds) {
        continue;
      }

      visited.add(li);
      const startPtId = cellPointIds[0];

      const loopPoints = [];
      loopPoints.push({ t: 0, ptId: startPtId });

      const endPtId = publicAPI.traverseLoop(
        input,
        Dir.Forward,
        li,
        startPtId,
        loopPoints
      );

      if (startPtId !== endPtId) {
        // didn't find a loop. Go other direction to see where we end up
        publicAPI.traverseLoop(input, Dir.Backward, li, startPtId, loopPoints);
        loopPoints.sort((a, b) => (a.t < b.t ? -1 : 1));
        // make closed contour
        if (
          loopPoints.length &&
          loopPoints[0].ptId !== loopPoints.at(-1)?.ptId
        ) {
          loopPoints.push({ ...loopPoints.at(-1)! });
        }
      }

      if (loopPoints.length) {
        loops.push(loopPoints);
      }
    }

    // clear output lines
    const outLines = output.getLines();
    outLines.resize(0);

    if (publicAPI.getMergeContours() && loops.length) {
      let largestIndex = 0;
      let longestLoop = 0;
      loops.forEach((loop, index) => {
        if (loop.length > longestLoop) {
          largestIndex = index;
          longestLoop = loop.length;
        }
      });
      outLines.insertNextCell(loops[largestIndex].map((pt) => pt.ptId));
    } else {
      loops.forEach((loop) => {
        outLines.insertNextCell(loop.map((pt) => pt.ptId));
      });
    }
  };

  publicAPI.requestData = (inData, outData) => {
    const [input] = inData;

    if (!outData[0]) {
      outData[0] = vtkPolyData.newInstance();
    }
    const [output] = outData;

    if (publicAPI.getConvexHull()) {
      publicAPI.computeConvexHull(input, output);
    } else {
      publicAPI.extractContours(input, output);
    }

    output.modified();
  };
}

// ----------------------------------------------------------------------------

const DEFAULT_VALUES = {
  mergeContours: false,
  convexHull: false,
};

// ----------------------------------------------------------------------------

export function extend(publicAPI: object, model: object, initialValues = {}) {
  Object.assign(model, DEFAULT_VALUES, initialValues);

  macro.obj(publicAPI, model);
  macro.algo(publicAPI, model, 1, 1);
  macro.setGet(publicAPI, model, ['mergeContours', 'convexHull']);

  vtkContourLoopExtractionCtor(publicAPI as vtkContourLoopExtraction, model);
}

// ----------------------------------------------------------------------------

export const newInstance = macro.newInstance(
  extend,
  'vtkContourLoopExtraction'
);

// ----------------------------------------------------------------------------

const vtkContourLoopExtraction: {
  newInstance: typeof newInstance;
  extend: typeof extend;
} = { newInstance, extend };

export default vtkContourLoopExtraction;
